import React, {useEffect, useState, useContext} from 'react'
import { AuthAlertContext } from '../../../AuthAlert/AuthAlertContext'



export const AddFrame = () => {
    const{setAlert} = useContext(AuthAlertContext)
    const [sampleImage, setSampleImage] = useState(null)
    const [imageFile, setImageFile] = useState({})
    const [price, setPrice] = useState(null);
    const [discount, setDiscount] = useState(0);
    const [status, setStatus] = useState(null);

    const addFrame = ()=>{
        if (imageFile) {
            if (price) {
                if (status) {
                    let dataForm = new FormData()
                    dataForm.append('discount', discount)
                    dataForm.append('price', price)
                    dataForm.append('status', status)
                    dataForm.append('file', imageFile, imageFile.name)
                    let framexhr = new XMLHttpRequest();
                    framexhr.open('POST', '/admin/add-frame', true)
                    framexhr.onload = ()=>{
                        if (framexhr.readyState ==4 && framexhr.status ==200) {
                            console.log(JSON.parse(framexhr.responseText));
                                if (framexhr.responseText) {
                                    if (JSON.parse(framexhr.responseText)) {
                                        if (JSON.parse(framexhr.responseText).response) {
                                            if (JSON.parse(framexhr.responseText).response.state) {
                                                console.log(framexhr.responseText);
                                                let formsInput = document.querySelectorAll('input')
                                                formsInput.forEach(input=>{
                                                    input.value = null
                                                })
                                                setStatus(null)
                                                setImageFile({})
                                                setPrice(null)
                                                setSampleImage(null)
                                                setDiscount(null)
                                                setAlert(JSON.parse(framexhr.responseText).response.message, 'success')
                                            }
                                        }
                                    }
                                }
                        }
                    }
                    framexhr.setRequestHeader('x-auth-token', localStorage.adminToken)
                    framexhr.send(dataForm)
                }
            }
        }
    }

    return (
        <div className="col-md-12">
            <div className="row justify-content-center">
               <div className="col-md-8" style={{background:"#fefefe", padding:30, borderRight:"20px solid #eee",  borderLeft:"20px solid #eee"}}>
                   <h2 className="p-3">Add new frame sample  </h2>
               <form>
                    {
                     sampleImage && <div className="form-group row border" style={{overflow:"hidden",background:`url(${sampleImage}) no-repeat center`, backgroundSize:"cover", height:250, width:"100%"}}>
                    </div>
                    }

                    <div className="form-group row">
                        <label htmlFor="colFormLabel">Frame sample image (required)</label>
                            <div className="col-12">
                            <input type="file" className="form-control border-0"  placeholder="message title" onInput={async (e)=>{
                                const file = e.target.files[0];
                                if (file) {
                                    const type = /image\//.test(file.type)
                                    if (type) {
                                        setImageFile(file)
                                        const reader = new FileReader();                              
                                        reader.addEventListener("load", async function () {
                                            await setSampleImage(reader.result);
                                        }, false);
                                        if (file) {
                                            reader.readAsDataURL(file);
                                          }
                                    }
                                }
                            }} />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="colFormLabel">Price (required)</label>
                            <div className="col-12">
                            <input type="number" className="form-control"  placeholder="frame price "  onChange={(e)=>{
                                setPrice(e.target.value)
                            }}/>
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="colFormLabel">Discount (optional)</label>
                            <div className="col-12">
                            <input type="number" className="form-control"  placeholder="discount" onChange={(e)=>{
                                setDiscount(e.target.value)
                            }}/>
                        </div>
                    </div>
                    <div className="form-group row">
                    <label className="my-1 mr-2">Frame status</label>
                    <select className="custom-select my-1 mr-sm-2" id="inlineFormCustomSelectPref" onChange={(e)=>{
                                setStatus(e.target.value)
                    }} required>
                        <option defaultValue>Choose...</option>
                        <option value="Available">Available</option>
                        <option value="Not_available">Not available</option>
                    </select>
                    </div>
                    <div className="form-group row">
                            <div className="col-12">
                            <input type="button" value="Add Frame Sample" className="form-control btn btn-info"  placeholder="col-form-label" 
                            onClick={()=>addFrame()}
                            />
                        </div>
                    </div>
                </form>
               </div>
            </div>
        </div>
    )
}

export default AddFrame
