import {BrowserRouter as Router, Route, Switch, Link} from 'react-router-dom'
import React, { useEffect } from 'react'

import AdminPrivateRoute from '../Routing/AdminPrivateRoute'
import {UsersJobStatus} from './AdminTabs/AdminPages/UsersJobStatus'
// import {SendMessage} from './AdminTabs/AdminPages/SendMessage'
import {Anouncement} from './AdminTabs/AdminPages/Anouncement'
import {AddSize} from './AdminTabs/AdminPages/AddSize'
import {AddFrame} from './AdminTabs/AdminPages/AddFrame'
import {AddPhotobookCover} from './AdminTabs/AdminPages/AddPhotobookCover'
import {ReplySupport} from './AdminTabs/AdminPages/ReplySupport'
import ModifySize from './AdminTabs/AdminPages/ModifySize'
import ModifyFrame from './AdminTabs/AdminPages/ModifyFrame'
import ModifyCover from './AdminTabs/AdminPages/ModifyCover'
import Users from './AdminTabs/AdminPages/Users'
import Panel from './Panel'
import AdminLogOut from './AdminTabs/AdminPages/AdminLogOut'
import { useContext } from 'react'
import AdminAuthContext from './AdminAuth/AdminAuthContext'


const AdminContent = () => {
    const {LoadUser} = useContext(AdminAuthContext)
    useEffect(() => {
    }, [])
    return (
        <div className="row px-3" style={{minHeight:400}}>
        <Router>
           <Switch>
                <AdminPrivateRoute exact path="/admin/users" component={Users} />
                <AdminPrivateRoute exact path="/admin/logout" component={AdminLogOut} />
                <AdminPrivateRoute exact path="/admin" component={Panel} />
                <AdminPrivateRoute exact path="/admin/jobs-status" component={UsersJobStatus} />
                {/* <AdminPrivateRoute exact path="/admin/send-message" component={SendMessage} /> */}
                <AdminPrivateRoute exact path="/admin/Anouncement" component={Anouncement} />
                <AdminPrivateRoute exact path="/admin/add-size" component={AddSize} />
                <AdminPrivateRoute exact path="/admin/add-frame" component={AddFrame} />
                <AdminPrivateRoute exact path="/admin/add-photobook-cover" component={AddPhotobookCover} />
                <AdminPrivateRoute exact path="/admin/reply-support" component={ReplySupport} />
                <AdminPrivateRoute exact path="/admin/modify-size" component={ModifySize} />
                <AdminPrivateRoute exact path="/admin/modify-frame" component={ModifyFrame} />
                <AdminPrivateRoute exact path="/admin/modify-cover" component={ModifyCover} />
           </Switch> 
        </Router>
        </div>
    )
}

export default AdminContent
