import React, { Component, useContext, useEffect, useState } from 'react'
import {AuthContext} from '../Auth/AuthState'

export const Sidebar = (props) =>  {
    const [user_name, setUser_name] = useState(null)
    const [user_id, setUser_id] = useState(null)
    const [user_image, setUser_image] = useState(null)

    const {state, Logout,LoadUser, isAuthenticated} = useContext(AuthContext)

    useEffect(() => {
        if (state) {
            if (state.user) {
                if (state.user.user_name) {
                    setUser_name(state.user.user_name)
                    setUser_id(state.user.user_id)
                    setUser_image(state.user.user_image)
                }
            }
        }
    }, [state.user, isAuthenticated])
    // boxShadow:"#ccc 0 0 10px",
        return (
            <div className="col-md-2  p-0" style={{overflowY:"scroll",background:"#095b70", overflowX:"hidden",minHeight:"100vh", borderRight:"20px solid #f5f5f5", boxShadow:"2px 4px 10px  0 #aaa",zIndex:2,  padding:10}} id="ktl-dashboard-sidebar">
                <div className="row" style={{minHeight:200, padding:20, color:"#fff",   fontFamily:"arial black",  borderBottom:"#f5f5f5 solid 1px",}}>
                <div className="col-12">
                    <span className="fa fa-bars"  id="ktl-dashboard-mobile-toggle" style={{fontSize:30, color:"#ff0080"}}
                    onClick={()=>{
                        let sidebar = document.querySelector("#ktl-dashboard-sidebar")
                        let main = document.querySelector("#ktl-dashboard-main")
                        sidebar.style.display == "none"?sidebar.style.display="block": sidebar.style.display="none";
                        main.className == "col-md-10" ? main.className = "col-md-12": main.className = "col-md-10";
                    }}>
                    </span> 

                </div>

                    <div className="col-md-12" style={{flex:1, display:"flex", justifyContent:"center", alignItems:"center",}}>
                       <div className="row " style={{display:"flex", alignItems:"center",flexDirection:"column", flex:1}}>
                            <div style={{display:"flex"}}>
                                 <img src={user_image} style={{width:80, border:'3px #fff solid',padding:3, height:80,objectFit:"cover",borderRadius:50}} alt="profile image " />

                                {/* <span className="fa fa-user-circle" style={{color:"#fff", fontSize:30}}></span> */}

                           </div>
                            <div style={{display:"flex"}}>
                            <span style={{color:"#fff"}}>{user_name}</span>
                            </div>
                           <div style={{display:"flex"}}>

                                <span style={{color:"#fff"}}>ID: {user_id}</span>
                           </div>
                       </div>
                    </div>
                </div>

                



               <div className="row " style={{justifyItems:"center", flex:1, paddingTop:20, paddingBottom:20, background:"#095b70",}} >
                   <div className="col-12">
                   <div className="row" style={{background:"#0a5b70",  borderRadius:10, color:"#333", marginBottom:10, marginLeft:10, fontFamily:"arial black", borderRadius:"10px", overflow:"hidden"}}>
                    <div className="col-12" style={{color:"#fff", textShadow:"0 1px 1px 0 #000",padding:12,cursor:'pointer',}}>
                        <b>
                            <strong>
                            <span className="fa fa-bell pr-3"  style={{fontSize:25, color:'#ccc', textShadow:"0 1px 1px 0 #000"}}></span> Notifications
                            </strong>
                        </b>
                    </div>
                </div>

                <div className="row" style={{background:"#0a5b70",  borderRadius:10, color:"#333", marginBottom:10, marginLeft:10, fontFamily:"arial black", borderRadius:"10px", overflow:"hidden"}} onClick={()=> window.location = "/dashboard/jobs-status"}>
                    <div className="col-12" style={{color:"#fff", textShadow:"0 1px 1px 0 #000",padding:12,cursor:'pointer',}} >
                        <b>
                            <strong>
                            <span className="fa fa-eye pr-3"  style={{fontSize:25, color:'#ccc', textShadow:"0 1px 1px 0 #000"}}></span> Jobs Status
                            </strong>
                        </b>
                    </div>
                </div>

                <div className="row" style={{background:"#0a5b70",  borderRadius:10, color:"#333", marginBottom:10, marginLeft:10, fontFamily:"arial black", borderRadius:"10px", overflow:"hidden"}} onClick={()=>window.location="/dashboard/profile"}>
                    <div className="col-12" style={{color:"#fff", textShadow:"0 1px 1px 0 #000",padding:12,cursor:'pointer',}}>
                    <b>
                        <strong>
                        <span className="fa fa-user pr-3"  style={{fontSize:25, color:'#ccc', textShadow:"0 1px 1px 0 #000"}}></span> View Profile
                        </strong>
                    </b>
                    </div>
                </div>
                <div className="row" style={{background:"#0a5b70",  borderRadius:10, color:"#333", marginBottom:10, marginLeft:10, fontFamily:"arial black", borderRadius:"10px", overflow:"hidden"}} onClick={()=> window.location = "/dashboard/support"}>
                    <div className="col-12" style={{color:"#fff", textShadow:"0 1px 1px 0 #000",padding:12,cursor:'pointer',}}>
                    <b>
                        <strong>
                        <span className="fas fa-envelope pr-3"  style={{fontSize:25, color:'#ccc', textShadow:"0 1px 1px 0 #000"}}></span> Support
                        </strong>
                    </b>
                    </div>
                </div>
                <div className="row" style={{background:"#0a5b70",  borderRadius:10, color:"#333", marginBottom:10, marginLeft:10, fontFamily:"arial black", borderRadius:"10px", overflow:"hidden"}} onClick={()=> window.location = "/dashboard/bookjob"} >
                    <div className="col-12" style={{color:"#fff", textShadow:"0 1px 1px 0 #000",padding:12,cursor:'pointer',}}>
                        <b>
                            <strong>
                            <span className="fa fa-file pr-3"  style={{fontSize:25, color:'#ccc', textShadow:"0 1px 1px 0 #000"}}></span> Book Job
                            </strong>
                        </b>
                    </div>
                </div>
                <div className="row" style={{background:"#0a5b70",  borderRadius:10, color:"#333", marginBottom:10, marginLeft:10, fontFamily:"arial black", borderRadius:"10px", overflow:"hidden"}} onClick={Logout}
                >
                    {/* <a href="/"></a> */}
                    <div className="col-12" style={{color:"#fff", textShadow:"0 1px 1px 0 #000",padding:12,cursor:'pointer',}}>
                    <b>
                        <strong>
                        <span className="fas fa-sign-out-alt pr-3"  style={{fontSize:25, color:'#ccc', textShadow:"0 1px 1px 0 #000"}}></span> Sign Out
                        </strong>
                    </b>
                    </div>
                </div>
                   </div>
               </div>
            </div>
        )
}

export default Sidebar
