import React, { useEffect, useContext, useState } from 'react'
import {AuthContext} from '../../../Auth/AuthState'
import { Fragment } from 'react'
import Axios from 'axios'
import Pagination from '../Bookjob/Pagination'
import { AuthAlertContext } from '../../../AuthAlert/AuthAlertContext'

export const  JobsStatus  = (props) => {

    const [userid, setUserid] = useState(null)
    const [userJobs, setUserJobs] = useState([])
    const [currentPage, setCurrentPage] = useState(1)
    const [postPerPage, setPostPerPage] = useState(10)

    const {state, isAuthenticated} = useContext(AuthContext)
    const {setAlert} = useContext(AuthAlertContext)

    useEffect(() => {
        if (state) {
            if (state.user) {
                if (state.user.user_id) {
                    setUserid(state.user.user_id)
                    getJobs()
                        // eslint-disable-next-line
                }
            }
        }

    }, [state])
    
    const dateFormater = new Intl.DateTimeFormat();
    const currencyFormater = new Intl.NumberFormat('en-NG', {
        style: 'currency',
        currency: 'NGN',
      })

    const getJobs =()=>{
        let xhr  = new XMLHttpRequest()
        xhr.open('GET', '/ktl-jobs-status', true)
        xhr.onload = function(){
            if (xhr.readyState == 4, xhr.status ==200) {
                if (JSON.parse(xhr.responseText)) {
                    if (JSON.parse(xhr.responseText).response) {
                        if (JSON.parse(xhr.responseText).response.state) {                    
                            setUserJobs(JSON.parse(xhr.responseText).response.result)
                        }
                    }
                }

            }
        }

        xhr.setRequestHeader('x-auth-token', localStorage.token)
        xhr.send();
    }
    const deleteJob = async(id)=>{
        if (id) {
            const res = await Axios.post('/ktl-delete-job', {id})
            try {
                console.log(res)
                if (res.data.response.state) {
                    setAlert(res.data.response.message, 'success')
                    getJobs()
                }
            } catch (error) {
                
            }
        }
    }

    const setPage = (number)=>{
        setCurrentPage(number)
    }

    const indexOfLastPost = currentPage * postPerPage;
    const indexOfFirstPost = (indexOfLastPost - postPerPage);
    const currentPost = userJobs.length > 0 ? userJobs.slice(indexOfFirstPost, indexOfLastPost): [];


    return (
        <div className="col-md-12 p-3" style={{overflowX:'auto'}}>


        <   section className="content">
            <div className="card" style={{overflowX:'hidden'}}>
                <div className="card-header">
                <h3 className="card-title">Your Jobs Status </h3>
                </div>
                <div className="card-body p-0" style={{display: 'block'}} style={{overflowX:'scroll'}}>
                <table className="table table-striped projects" >
                    <thead>
                        <tr>
                            <th scope="col">Job ID</th>
                            <th scope="col">Date Submited</th>
                            <th scope="col">Status</th>
                            <th scope="col"> ₦Status</th>
                            <th scope="col">Amount</th>
                            <th scope="col">Job Type</th>
                            <th scope="col">Specification</th>
                            <th scope="col">Copies</th>
                            <th scope="col">Size</th>
                            <th scope="col">Action</th>
                            <th scope="col">Date Completed</th>
                        </tr>
                    </thead>
                    <tbody>


                    {currentPost.map((job, key)=>(

                        <tr className="project-actions text-right" key={key} >
                            <th>{job.id}</th>
                            <td>{job.added_on}</td>
                            <td>{job.job_status}</td>
                            <td>{job.payment_status!=='completed'?(
                                <Fragment>
                                    <a href={job.payment_url} className="border btn btn-primary">
                                        Pay now
                                    </a>
                                </Fragment>
                            ):(
                                <Fragment>
                                <span  className="border btn btn-success">
                                    Paid
                                </span>
                            </Fragment>
                            )}</td>
                            <td>
                            <span style={{fontFamily:"arial black"}}>
                            {currencyFormater.format(job.bill)}
                                
                                </span>
                            </td>
                            <td>{job.job_type}</td>
                            <td>{job.job_extra}</td>
                            <td>{job.copies}</td>
                            <td>{job.job_size}</td>
                            <td>
                                {job.payment_status !== 'completed' ? (
                                <a className="btn btn-danger btn-sm" href="#" onClick={()=>deleteJob(job.id)}>
                                    <i className="fas fa-trash"></i>Delete
                                </a>
                                ):(
                                <a className="btn btn-primary btn-sm" href={`/dashboard/upload-job/${job.reference}`}>
                                    <i className="fas fa-upload"></i>Upload
                                </a>
                                )}
                            </td>
                            <td>{job.completed_on}</td>
                        </tr>
                        ))}


                        {/* <tr>
                            
                            <td className="project-actions text-right">
                                <a className="btn btn-primary btn-sm" href="#">
                                    <i className="fas fa-folder">
                                    </i>
                                    View
                                </a>
                                <a className="btn btn-info btn-sm" href="#">
                                    <i className="fas fa-pencil-alt">
                                    </i>
                                    Edit
                                </a>
                                <a className="btn btn-danger btn-sm" href="#">
                                    <i className="fas fa-trash">
                                    </i>
                                    Delete
                                </a>
                            </td>
                        </tr> */}
                        
                    
                    </tbody>
                </table>
                </div>
                <Pagination postPerPage={postPerPage} setPage={setPage} totalPost={userJobs.length} />

            </div>

            </section>
        </div>
    )
}

export default JobsStatus
