import React, { useState, useEffect } from 'react'
import Pagination from './Pagination'

 const Covers = ({setJobExtra, setExtra, jobBill, setJobBill}) =>{

    const [posts, setPosts] = useState([])
    const [currentPage, setCurrentPage] = useState(1)
    const [postPerPage, setPostPerPage] = useState(6)
    const [totalPost, setTotalPost] = useState(null)
    const [selected , setSelected] = useState(null)
    const [typeId, setTypeId] = useState(null);
    const [type, setType] = useState(null);

     useEffect(() => {

        const getCovers= async()=>{
            let xhr = new XMLHttpRequest();
            xhr.open('GET', '/ktl-photobook-covers', true);
            xhr.onload =  async()=>{
                if (xhr.readyState == 4 && xhr.status == 200) {
                    if(xhr.responseText){
                        if (JSON.parse(xhr.responseText)) {
                            if (JSON.parse(xhr.responseText).response) {
                                if (JSON.parse(xhr.responseText).response.state) {
                                    await setPosts(JSON.parse(xhr.responseText).response.result);
                                }
                            }
                        }
                    }
                }
            }
            xhr.setRequestHeader('x-auth-token', localStorage.token)
            xhr.send();
        }

        getCovers();

    }, [])

    const setPage = (number)=>{
        setCurrentPage(number)
    }

    const indexOfLastPost = currentPage * postPerPage;
    const indexOfFirstPost = (indexOfLastPost - postPerPage);
    const currentPost = posts.slice(indexOfFirstPost, indexOfLastPost);

    return (
        <div className="row p-2">

        <div className="col-md-12 justify-content-center" style={{overflow:"hidden", background:"#f3f3f3",}}>
            <div className="row ">
                <div className="col-md-12">
                    <div className="row" style={{background:"#f5f5f5"}}>
                        <div className="col-md-12 p-2" style={{background:"#eee", borderBottom:'1px solid #ccc'}}>
                            <h2 className="lead ">Select Photobook Type </h2>
                        </div>
                        <div className="col-md-12">
                            <div className="row">
                                <div className="col-md-12 p-4" id="ktl-synthetic-pb" style={{background:"#27424a", marginBottom:1, color:"#fff", textTransform:"uppercase",fontFamily:"arial black", fontSize:20}}
                                    onClick={(e)=>{
                                        if (e.target.id  == "ktl-synthetic-pb" || e.target.id  == "ktl-layflat-pb") {
                                            if (typeId) {
                                                let firstSelected = document.querySelector(`#${typeId}`)
                                                if (firstSelected) {
                                                    firstSelected.style.border = "0px solid #fff"
                                                }
                                                
                                            }
                                            if (selected) {
                                                let s = document.querySelector(`#${selected}`);
                                                if (s) {
                                                    setSelected(null);
                                                    s.style.borderColor = "#eee"
                                                    setJobExtra({jobExtra:null});
                                                    setExtra(null)
                                                }
                                            } 

                                            setTypeId(`ktl-synthetic-pb`);
                                            setType("Synthetic Photobook");
                                            e.target.style.border = "10px solid #ff0080"
                                        }
                                    }}
                                >
                                    Synthetic 
                                </div>
                                <div className="col-md-12 p-4" id="ktl-layflat-pb" style={{background:"#27424a", color:"#fff", textTransform:"uppercase",fontFamily:"arial black", fontSize:20}} 
                                        onClick={(e)=>{

                                        // setJobExtra({jobExtra:cover.id});
                                        // e.target.style.border = "10px solid #ff0080"
                                        if (e.target.id  == "ktl-synthetic-pb" || e.target.id  == "ktl-layflat-pb") {
                                            if (typeId) {
                                                let firstSelected = document.querySelector(`#${typeId}`)
                                                if (firstSelected) {
                                                    firstSelected.style.border = "0px solid #fff"
                                                }
                                                
                                            }
                                           
                                            if (selected) {
                                                let s = document.querySelector(`#${selected}`);
                                                if (s) {
                                                    setSelected(null);
                                                    s.style.borderColor = "#eee"
                                                    setJobExtra({jobExtra:null});
                                                    setExtra(null)
                                                }

                                            } 

                                            
                                            setTypeId(`ktl-layflat-pb`);
                                            setType("Layflat (Hard Cover) Photobook");
                                            e.target.style.border = "10px solid #ff0080"
                                        }
                                        
                                    }}
                                >
                                    Layflat (Hard cover) 
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>


            <div className="col-md-12 justify-content-center " style={{overflow:"hidden", background:"#f3f3f3"}}>
                <div className="row ">
                    <div className="col-md-12">
                        <div className="row" style={{background:"#f5f5f5"}}>
                          <div className="col-md-12 p-4" style={{background:"#eee", borderBottom:'1px solid #ccc'}}>
                             <h2 className="lead ">Pick cover for your photobook</h2>
                          </div>
                        </div>
                    </div>
                    <div className="col-md-12">
                        <div className="row p-2">
                            {currentPost.map((cover,key)=>(
                            <div key={key} className="col-md-4" id={'ktl-cover-'+key} key={cover.id} onClick={(e)=>{
                                        if (selected) {
                                            let firstSelected = document.querySelector(`#${selected}`)
                                            if (firstSelected) {
                                                firstSelected.style.borderColor = "#eee"
                                            }
                                        }
                                        if (type) {
                                            setSelected(`ktl-cover-${key}`);
                                            setJobExtra({jobExtra:type + ", with cover id: " + cover.id});
                                            setExtra(1)
                                            setJobBill({frameCoverPrice:cover.price})
                                            e.target.style.borderColor = "#ff0080"
                                        }
                                    }
                                }
                            style={{height:200,border:"10px solid #eee", background:`url("${'/'+cover.cover_url}") no-repeat center center`, backgroundSize:'cover'}}
                            >
                            </div>
                            ))
                            }
                        </div>
                    </div>
                    <div className="col-md-12">
                        <div className="row px-4">
                        <Pagination postPerPage={postPerPage} setPage={setPage} totalPost={posts.length} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Covers