import React, { Component, useEffect, useState, useContext } from 'react'
import AdminAlert from '../AuthAlert/AuthAlert'
import { AuthAlertContext } from '../AuthAlert/AuthAlertContext'
import AdminAuthContext from './AdminAuth/AdminAuthContext'

export const AdminHeader =()=> {
    const adminCxt = useContext(AuthAlertContext)
    const authCxt = useContext(AdminAuthContext)
    const [path, setPath] = useState(null)
    useEffect(() => {
        let p = window.location.pathname
        let eachpath =  p.split('/dashboard/', 2);
        setPath(eachpath[1])
    }, [path])


    return (
        <div className="row mb-4 " style={{borderBottom:"5px solid #f5f5f5",}}>
            <div className="col-md-12 " style={{padding:20,background:"#f5f5f5", boxShadow:"#ccc 0px 0 3px"}}>
               <div className="row ">
                   <div className="col-md-12 align-text-right items-align-center">
                        <div className="row">
                            <div className="col-4">
                                <span className="fa fa-bars"  id="ktl-dashboard-desktop-toggle" style={{fontSize:30, color:"#ff0080"}}
                                onClick={()=>{
                                    let sidebar = document.querySelector("#ktl-dashboard-sidebar")
                                    let main = document.querySelector("#ktl-dashboard-main")
                                    sidebar.style.display == "none"?sidebar.style.display="block": sidebar.style.display="none";
                                    main.className == "col-md-9" ? main.className = "col-md-12": main.className = "col-md-9";
                                }}>
                                </span> 

                                <span className="fa fa-bars"  id="ktl-dashboard-mobile-toggle" style={{fontSize:30, color:"#ff0080"}}
                                onClick={()=>{
                                    let sidebar = document.querySelector("#ktl-dashboard-sidebar")
                                    let main = document.querySelector("#ktl-dashboard-main")
                                    sidebar.style.display == "none"?sidebar.style.display="block": sidebar.style.display="none";
                                    main.className == "col-md-9" ? main.className = "col-md-12": main.className = "col-md-9";
                                }}>
                                </span> 

                            </div>
                            <div className="col-8 ">
                                <div className="row">
                                    <div className="col-7 ">
                                        <a href="/admin" style={{textDecoration:"none"}}>
                                        <span style={{fontFamily:"arial black",color:"#587f8A", textTransform:"uppercase",padding:10}}>KTL Admin Panel</span> 
                                        </a>
                                    </div>
                                    <div className="col-5  text-right">
                                        <div className="row">
                                        {/* <div className="col-4">
                                                <a href="/admin/users" style={{textDecoration:"none"}}>
                                                <span className="fa fa-users"></span>
                                                </a>
                                            </div>
                                            <div className="col-4">
                                                 <a href="/admin/reply-support" style={{textDecoration:"none"}}>
                                                <span className="fa fa-bell-o"></span>
                                                </a>
                                            </div> */}
                                            <div className="col-8"></div>
                                            <div className="col-4">
                                                 <span onClick={authCxt.Logout}  style={{textDecoration:"none"}}>
                                                <span className="fas fa-sign-out-alt"></span>
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                   </div>
               </div>
            </div>
            <div className="col-md-12" style={{background:"#fff", display:'flex', boxShadow:"#ccc 0 0 0px", padding:20}}>
               <span className="fa">ktl-panel</span>
                <span className=" px-3 fa fa-angle-double-right">  {path} </span>
            </div>
            <AdminAlert />
        </div>
    )
}

export default AdminHeader
