import React, { useReducer } from 'react'
import AuthAlertReducer from './AuthAlertReducer'
import { AuthAlertContext } from './AuthAlertContext'

const AuthAlertState = (props) => {
    const initialstate =  []
    const [state, dispatch] = useReducer(AuthAlertReducer, initialstate)

    // initiate alert 
    const setAlert = (msg, type)=>{
        const id = Math.ceil(Math.random() * 128874747483837399)
        dispatch({msg, id, type})
        setTimeout(() => {
            return dispatch({msg:null, id, type:'clearAlerts'})
        }, 5000);
    }
    return (
        <AuthAlertContext.Provider value={{
            setAlert,
            state
        }}>
             {props.children}
        </AuthAlertContext.Provider>
    )
}

export default AuthAlertState
