import React, { Component } from 'react'
import {BrowserRouter as Router,useLocation,Route,Link, Switch} from 'react-router-dom'
import {BookJob} from './Users/Bookjob/BookJob'
import {UploadJob} from './Users/Bookjob/UploadJob'
import {Profile} from './Users/Profile/Profile'
import {Support} from './Users/Support/Support'
import {Notifications} from './Users/Notifications/Notifications'
import {JobsStatus} from './Users/JobsStatus/JobsStatus'
import {PaymentDetail} from './Users/PaymentDetail/PaymentDetail'
import PrivateRoute from '../Routing/PrivateRoute'
import UserPanel from './Users/Bookjob/UserPanel'

const Content = () => {

        return (
            <div className="row justify-content-center" style={{minHeight:500,bottom:0}}>
            <Router>
            <Switch>
            <PrivateRoute exact path="/dashboard/" component={UserPanel} />
            <PrivateRoute exact path="/dashboard/upload-job/:reference" component={UploadJob} />
            <PrivateRoute exact path='/dashboard/ktl-payment-detail'>
                    <PaymentDetail/>
                </PrivateRoute>
            <PrivateRoute exact path="/dashboard/bookjob">
                    <BookJob/>
                </PrivateRoute>
                <PrivateRoute exact path="/dashboard/profile">
                    <Profile/>
                </PrivateRoute>
                <PrivateRoute exact path="/dashboard/support">
                    <Support/>
                </PrivateRoute>
                <PrivateRoute exact path="/dashboard/notifications">
                    <Notifications/>
                </PrivateRoute>
                <PrivateRoute exact path="/dashboard/jobs-status">
                    <JobsStatus/>
                </PrivateRoute>
            </Switch> 
            </Router>
        </div>
        )
}

export default Content
