import React, { Component } from 'react'

export class SelectJobType extends Component {
    setJobType=(type)=>{
        this.props.setJobType({jobType: type, currentView: 'SelectSize'})
    }
    render() {
        
        return (
            <div className="col-md-7">
            <div className="info-box mt-3">
                <span className="info-box-icon bg-info"><i className="fas fa-photo-video"></i></span>
                <div className="info-box-content">
                <button onClick={()=>this.setJobType('Enlargement')} type="button" className="btn btn-block btn btn-lg">
                    <h2>Enlargement</h2>
                </button>
                </div>
            </div>
            <div className="info-box mt-3">
                <span className="info-box-icon bg-info"><i className="fas fa-photo-video"></i></span>
                <div className="info-box-content">
                <button onClick={()=>this.setJobType('Photobook')} type="button" className="btn btn-block btn btn-lg">
                    <h2>Photobook</h2>
                </button>
                </div>
            </div>
            <div className="info-box mt-3">
                <span className="info-box-icon bg-info"><i className="fas fa-photo-video"></i></span>
                <div className="info-box-content">
                <button onClick={()=>this.setJobType('Potrait')} type="button" className="btn btn-block btn btn-lg">
                    <h2>Potrait Printing</h2>
                </button>
                </div>
            </div>
            </div>
        )
    }
}

export default SelectJobType
