import React, { Component, Fragment } from 'react'
import {AuthContext} from '../Auth/AuthState'
import AuthAlert from '../AuthAlert/AuthAlert'

export class Header extends Component {
    state = {
        path: null
    }

    componentDidMount(){
        let path = window.location.pathname
        let eachpath =  path.split('/dashboard/', 2);
        this.setState({path: eachpath[1]})
    }
    render() {
        return (
            <Fragment>
            <div className="row mb-4 " style={{borderBottom:"2px solid #ccc",boxShadow:"10px 2px 6px 0 #333"}}>
                <div className="col-md-12 " style={{padding:20,background:"#f5f5f5", boxShadow:"#ccc 0px 0 3px"}}>
                   <div className="row ">
                       <div className="col-md-12 align-text-right items-align-center">
                            <div className="row">
                                <div className="col-4">
                                    <span className="fa fa-bars"  id="ktl-dashboard-desktop-toggle" style={{fontSize:30, color:"#ff0080", cursor:'pointer'}}
                                    onClick={()=>{
                                        let sidebar = document.querySelector("#ktl-dashboard-sidebar")
                                        let main = document.querySelector("#ktl-dashboard-main")
                                        sidebar.style.display == "none"?sidebar.style.display="block": sidebar.style.display="none";
                                        main.className === "col-md-10" ? main.className = "col-md-12": main.className = "col-md-10";
                                    }}>
                                    </span> 

                                    <span className="fa fa-bars"  id="ktl-dashboard-mobile-toggle" style={{fontSize:30, color:"#ff0080", cursor:'pointer'}}
                                    onClick={()=>{
                                        let sidebar = document.querySelector("#ktl-dashboard-sidebar")
                                        let main = document.querySelector("#ktl-dashboard-main")
                                        sidebar.style.display == "none"?sidebar.style.display="block": sidebar.style.display="none";
                                        main.className === "col-md-10" ? main.className = "col-md-12": main.className = "col-md-10";
                                    }}>
                                    </span> 

                                </div>
                                <div className="col-8 ">
                                    <div className="row">
                                        <div className="col-7 ">
                                            <a href="/dashboard" style={{textDecoration:"none"}}>
                                            <span style={{fontFamily:"arial black",color:"#587f8A", textTransform:"uppercase",padding:10}}>User Dashboard</span> 
                                            </a>
                                        </div>
                                        <div className="col-5  text-right">
                                            <div className="row text-right">
                                                <div className=" col-8 text-right">
                                                    <a  className="text-reset text-right" href="/dashboard/bookjob"> 
                                                            <span className=" bg-info" style={{borderRadius:4}}><i className="fas fa-folder p-2"></i></span>
                                                    </a>
                                                </div>
                                                <div className="col-4">
                                                     <a onClick={this.context.Logout} style={{cursor:'pointer'}}><span className="fas fa-sign-out-alt text-primary"></span></a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                       </div>
                   </div>
                </div>
                <div className="col-md-12" style={{background:"#fff", display:'flex', boxShadow:"#ccc 0 0 0px", padding:20}}>
                   <span className="fa">dashboard</span>
                    <span className=" px-3 fa fa-angle-double-right">  {this.state.path} </span>
                </div>
            </div>
            <div className="col-md-12">
            <AuthAlert />
            </div>
            </Fragment>
        )
    }
}
Header.contextType = AuthContext
export default Header
