import React, { Component } from 'react'
import Header from './Header'
import Content from './Content'
import Footer from './Footer'

const Main = () => {
        return (
            <div className="col-md-10" id="ktl-dashboard-main" style={{background:"#fff"}}> 
                <div className="row">
                    <div className="col-md-12">
                        <Header />
                        <Content />
                        <Footer />
                    </div>
                </div>
            </div>
        )
}

export default Main
