import React, { useState, useEffect, Fragment, useContext } from 'react'
import {AuthAlertContext} from '../../../AuthAlert/AuthAlertContext'

export const ModifyFrame = () => {
    const adminCxt = useContext(AuthAlertContext)
    const [frames, setFrames] = useState([])
    const [previewUrl, setPreviewUrl] = useState(null) 
    const [modifingid, setModifingid] = useState(null) 
    const [status, setStatus] = useState('') 
    const [price, setPrice] = useState('') 
    const [modal, setModal] = useState("none") 
    const [frameid, setFrameid] = useState(null) 
    const [framesapi, setFramesapi] = useState("ktl-frames") 
    const [modstatus, setModstatus] = useState(null) 


    useEffect(() => {
        
        getFrames()
    }, [framesapi])

    const getFrames = ()=>{
        let fxhr = new XMLHttpRequest();
        fxhr.open('GET', `/admin/${framesapi}`, true)
        fxhr.onload = ()=>{
            if (fxhr.status==200 && fxhr.readyState == 4) {
                if (fxhr.responseText) {
                    if (JSON.parse(fxhr.responseText)) {
                        if (JSON.parse(fxhr.responseText).response) {
                            if (JSON.parse(fxhr.responseText).response.state) {
                                console.log('frames sss')
                                setFrames(JSON.parse(fxhr.responseText).response.result)
                            }
                        }
                    }
                }
            }
        }
        fxhr.setRequestHeader('x-auth-token', localStorage.adminToken)
        fxhr.send();
    }

    const updateFrame= ()=>{
        if (price) {
            if (status) {
                let dataform = new FormData()
                dataform.append('status', status)
                dataform.append('price', price)
                dataform.append('id', frameid)
                let ufxhr = new XMLHttpRequest();
                ufxhr.open('POST', '/admin/modify-frame', true)
                ufxhr.onload = ()=>{
                    if (ufxhr.status == 200 && ufxhr.readyState==4) {
                        if (ufxhr.responseText) {
                            if (JSON.parse(ufxhr.responseText)) {
                                if (JSON.parse(ufxhr.responseText).response) {
                                    if (JSON.parse(ufxhr.responseText).response.state) {
                                        console.log(ufxhr.responseText)
                                        setFramesapi("ktl-frames")
                                        setModal("none")
                                        adminCxt.setAlert(JSON.parse(ufxhr.responseText).response.message,'success')
                                        getFrames()
                                    }
                                }
                            }
                        }
                    }
                }
                ufxhr.setRequestHeader('x-auth-token', localStorage.adminToken)
                ufxhr.send(dataform)
            }
        }
    }
    const deleteFrame = ()=>{
        if (frameid) {
            let dataform = new FormData();
            dataform.append('id', frameid)
            let delxhr = new XMLHttpRequest();
            delxhr.open('POST', '/admin/delete-frame', true)
            delxhr.onload = ()=>{
                if (delxhr.readyState ==4 && delxhr.status) {
                    if (delxhr.responseText) {
                        if (JSON.parse(delxhr.responseText)) {
                            if (JSON.parse(delxhr.responseText).response) {
                                if (JSON.parse(delxhr.responseText).response.state) {
                                    setFramesapi("ktl-frames")
                                    setModal("none")
                                    adminCxt.setAlert(JSON.parse(delxhr.responseText).response.message,'danger')
                                    getFrames()
                                }
                            }
                        }   
                    }
                }
            }
            delxhr.setRequestHeader('x-auth-token', localStorage.adminToken)
            delxhr.send(dataform);
        }

    }
    return (
        <div className="col-md-12 border ">
            
            <div className="" style={{display:`${modal}`,overflowY:'auto', overflowX:"hidden",justifyContent:"center", alignItems:"center", flex:1, position:"fixed",bottom:0, top:0,left:0,right:0, width:"100%", height:"100%", background:"linear-gradient(90deg, rgba(207, 207, 207,0.8), rgba(207, 207, 207,0.8))", zIndex:6}}>
                <div className="" style={{display:"flex"}}>
                   <div className="col-md-12 " style={{}}>
                       <div className="row justify-content-center align-items-center" style={{background:"#f3f3f3", overflow:"hidden", borderRadius:12, padding:20}}>
                          <div className="row justify-content-center">
                           <div className="mb-3 p-3 text-center text-danger" style={{borderRadius:50, position:"fixed", top:10, right:10, zIndex:5}} 
                            onClick={()=>{
                                setModal('none')
                             }}> 
                           <span className={'fa fa-times'} style={{fontSize:50}}>
                                </span>
                           </div>

                           <div className="col-md-5" style={{overflow:"hidden", maxHeight:300, maxWidth:500,borderRadius:20}}>
                           <div className="row" style={{position:"absolute"}}>
                                   <div>
                            <span className="fa fa-tag" style={{borderRadius:"100%",fontSize:40, padding:20, background:"#fff"}}>{' '+frameid}</span>
                                   </div>
                               </div>
                           <img src={'/'+previewUrl} alt="" style={{height:'inherit', width:'inherit'}}/>

                           </div>
                           <div className="col-md-2" style={{minHeight:300, minWidth:500}}>

                               <div className="row">
                                   <div className="col-md-12 bg-light p-4" style={{borderRadius:5}}>
                                   <div className="form-group mb-5">
                                    <span className="btn btn-danger form-control" 
                                        onClick={()=>deleteFrame(modifingid)}
                                    >
                                            Delete frame {frameid}
                                        </span>
                                    </div>
                                   <div className="form-group ">
                            <label htmlFor="ktl_frame_status">Status: CURRENT STATUS: {modstatus}</label>
                                    <select className="form-control mb-3" id="ktl_frame_status" onChange={async (e)=>{
                                        setStatus(e.target.value)
                                        console.log(status, 'target value for the status ')
                                    }} required>
                                        <option >Choose ...</option>
                                            <option value="Available">Available</option>
                                            <option value="Not_available">Not available</option>
                                    </select>
                                    </div>
                                    <div className="form-group mb-3">
                                    <label htmlFor="ktl_frame_price">Price</label>

                                    <input type="number" className="form-control" id="ktl_frame_price" value={price} onChange={(e)=>{
                                        setPrice(e.target.value);
                                    }}/>
                                    </div>
                                    <div className="form-group mb-3"
                                        onClick={()=>updateFrame()}
                                    >
                                    <span className="btn btn-success form-control"
                                    >
                                        Save changes on frame {frameid}
                                        </span>
                                    </div>
                                   </div>
                               </div>
                            </div>
                       </div>
                   </div>
                   </div>
                </div>
            </div>

            <div className="row" style={{background:"#f5f5f5", padding:20}}>
            {frames.map(sample=>(
                    <div className="col-md-3" key={sample.id} style={{overflow:"hidden", border:"10px solid #eee",}}>
                        <div className="row justify-content-center align-items-center" style={{overflow:"hidden", height:200,width:"inherit", backgroundSize:"cover"}}>
                        <img src={'/'+sample.image_url} style={{height:'auto',width:"inherit"}} alt=""/>
                            <div className="col-6 text-left" style={{position:"absolute"}}>
                                <span className="btn p-2 border-info" style={{background:"#9ccedb", color:"#000", fontFamily:"arial black",}} onClick={()=>{
                                setPreviewUrl(sample.image_url)
                                setModal('flex')
                                setPrice(sample.price)
                                console.log(sample.status, 'the real sample status ')
                                setFrameid(sample.id)
                                setStatus(sample.status)
                                setModstatus(sample.status)
                                 }}> Modify</span>
                            </div>
                        </div>
                        <div className="row justify-content-center align-items-center" style={{minHeight:50, backgroundColor:"#dddd", borderTop:`5px solid ${sample.status == 'Available' || sample.status == 'available'?'green':'red'}`}}>
                            <div className="col-5">
                                <span className="fa fa-tag" style={{background:"#ffedf6", borderRadius:50 ,padding:10}}>
                                 {sample.id}
                                </span>
                            </div>
                            
                            <div className="col-5">
                                <span className="" style={{color:"#ff0080",fontFamily:"arial black", padding:10}}>
                                  #{sample.price}
                                </span>
                            </div>
                        </div>
                    </div>
            ))}
            </div>
        </div>
    )
}

export default ModifyFrame
