import React from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
// Pages 
import AdminDashboard from './components/Pages/AdminDashboard'
import Home from './components/Pages/Home'
import Dashboard from './components/Pages/Dashboard'
import Register from './components/Pages/Register'
import Login from './components/Pages/Login'
import Logout from './components/Pages/Logout'
import ForgottenPassword from './components/Pages/ForgottenPassword'
import AuthState  from './components/Auth/AuthState'
import setUserToken from './components/utils/setUserToken'
import setAdminToken from './components/utils/setAdminToken'
import PrivateRoute from './components/Routing/PrivateRoute'
import AdminPrivateRoute from './components/Routing/AdminPrivateRoute'
import AdminLogin from './components/AdminDashboard/AdminAuth/AdminLogin';
import AdminAUthState from './components/AdminDashboard/AdminAuth/AdminAuthState';

import AuthAlertState from './components/AuthAlert/AuthAlertState'


if (localStorage.token) {
  setUserToken(localStorage.token)
}

if (localStorage.adminToken) {
  setAdminToken(localStorage.adminToken)
}

function App() {  
  return (
    <AdminAUthState>
      <AuthState>
        <AuthAlertState>
            <Router>
              <div className="container-fluid p-0">
              <Switch>
                <Route exact path="/" component={Home}/>
                <Route exact path="/admin/login" component={AdminLogin}/>
                <Route exact path="/login" component={Login}/>
                <Route exact path="/logout" component={Logout}/>
                <AdminPrivateRoute exact path="/admin/" component={AdminDashboard}/>
                <AdminPrivateRoute exact path="/admin/:page" component={AdminDashboard}/>
                <AdminPrivateRoute exact path="/admin/:page/:subpages" component={AdminDashboard}/>
                <PrivateRoute exact path="/dashboard" component={Dashboard}/>
                <PrivateRoute exact path="/dashboard/:page" component={Dashboard}/>
                <PrivateRoute exact path="/dashboard/:page/:query" component={Dashboard}/>
                <Route exact path="/register" component={Register}/>
                <Route exact path="/forgotten-password" component={ForgottenPassword}/>
              </Switch>
              </div>
            </Router>
        </AuthAlertState>
      </AuthState>
    </AdminAUthState>
  );
}

export default App;

