import React, { useState, useEffect, Fragment, useContext } from 'react'
import Pagination from './Pagination'

const Frames = ({setJobExtra, setExtra, jobBill, setJobBill}) =>{

    const [frames, setFrames] = useState([])
    const [previewUrl, setPreviewUrl] = useState(null) 
    const [modifingid, setModifingid] = useState(null) 
    const [status, setStatus] = useState('') 
    const [price, setPrice] = useState('') 
    const [modal, setModal] = useState("none") 
    const [frameid, setFrameid] = useState(null) 
    const [framesapi, setFramesapi] = useState("ktl-frames") 
    const [modstatus, setModstatus] = useState(null) 


        const [posts, setPosts] = useState([])
        const [currentPage, setCurrentPage] = useState(1)
        const [postPerPage, setPostPerPage] = useState(6)
        const [totalPost, setTotalPost] = useState(null)
        const [selected , setSelected] = useState(null)
        const [typeId, setTypeId] = useState(null);

    useEffect(() => {
        
        getFrames()
    }, [framesapi])

    const currencyFormater = new Intl.NumberFormat('en-NG', {
        style: 'currency',
        currency: 'NGN',
      })

    const getFrames = ()=>{
        let fxhr = new XMLHttpRequest();
        fxhr.open('GET', `/${framesapi}`, true)
        fxhr.onload = ()=>{
            if (fxhr.status==200 && fxhr.readyState == 4) {
                if (fxhr.responseText) {
                    if (JSON.parse(fxhr.responseText)) {
                        if (JSON.parse(fxhr.responseText).response) {
                            if (JSON.parse(fxhr.responseText).response.state) {
                                setFrames(JSON.parse(fxhr.responseText).response.result)
                                console.log(JSON.parse(fxhr.responseText), 'frames ')
                            }
                        }
                    }
                }
            }
        }
        fxhr.setRequestHeader('x-auth-token', localStorage.token)
        fxhr.send();
    }



    const updateFrame= ()=>{
        if (price) {
            if (status) {
                let dataform = new FormData()
                dataform.append('status', status)
                dataform.append('price', price)
                dataform.append('id', frameid)
                let ufxhr = new XMLHttpRequest();
                ufxhr.open('POST', '/admin/modify-frame', true)
                ufxhr.onload = ()=>{
                    if (ufxhr.status == 200 && ufxhr.readyState==4) {
                        if (ufxhr.responseText) {
                            if (JSON.parse(ufxhr.responseText)) {
                                if (JSON.parse(ufxhr.responseText).response) {
                                    if (JSON.parse(ufxhr.responseText).response.state) {
                                        console.log(ufxhr.responseText)
                                        setFramesapi("ktl-frames")
                                        setModal("none")
                                    }
                                }
                            }
                        }
                    }
                }
                ufxhr.send(dataform)
            }
        }
    }

    
    const deleteFrame = ()=>{
        if (frameid) {
            let dataform = new FormData();
            dataform.append('id', frameid)
            let delxhr = new XMLHttpRequest();
            delxhr.open('POST', '/admin/delete-frame', true)
            delxhr.onload = ()=>{
                if (delxhr.readyState ==4 && delxhr.status) {
                    if (delxhr.responseText) {
                        if (JSON.parse(delxhr.responseText)) {
                            if (JSON.parse(delxhr.responseText).response) {
                                if (JSON.parse(delxhr.responseText).response.state) {
                                    setFramesapi("ktl-frames")
                                    setModal("none")
                                    getFrames()
                                }
                            }
                        }   
                    }
                }
            }
            delxhr.send(dataform);
        }

    }

    const setPage = (number)=>{
        setCurrentPage(number)
    }

    const indexOfLastPost = currentPage * postPerPage;
    const indexOfFirstPost = (indexOfLastPost - postPerPage);
    const currentPost = frames.slice(indexOfFirstPost, indexOfLastPost);

    return (
        <div className="col-md-12 border ">
            
            <div className="" style={{display:`${modal}`,overflowY:'auto', overflowX:"hidden",justifyContent:"center", alignItems:"center", flex:1, position:"fixed",bottom:0, top:0,left:0,right:0, width:"100%", height:"100%", background:"linear-gradient(90deg, rgba(207, 207, 207,0.8), rgba(207, 207, 207,0.8))", zIndex:6}}>
                <div className="" style={{display:"flex"}}>
                   <div className="col-md-12 " style={{}}>
                       <div className="row justify-content-center align-items-center" style={{background:"#f3f3f3", overflow:"hidden", borderRadius:12, padding:20}}>
                          <div className="row justify-content-center">
                           <div className="mb-3 p-3 text-center text-danger" style={{borderRadius:50, position:"fixed", top:10, right:10, zIndex:5}} 
                            onClick={()=>{
                                setModal('none')
                                console.log('you just clicked me ')
                             }}> 
                           <span className={'fa fa-times'} style={{fontSize:50}}>
                                </span>
                           </div>

                           <div className="col-md-5" style={{overflow:"hidden", minHeight:300, minWidth:500, background:`url('/${previewUrl}') no-repeat center center `, borderRadius:20}}>
                               <div className="row">
                                   <div>
                            <span className="fa fa-tag" style={{borderRadius:"100%",fontSize:40, padding:20, background:"#fff"}}>{' '+frameid}</span>
                                   </div>
                               </div>

                           </div>
                           <div className="col-md-2" style={{minHeight:300, minWidth:500}}>

                               <div className="row">
                                   <div className="col-md-12 bg-light p-4" style={{borderRadius:5}}>
                                   <div className="form-group ">
                                    <label>Status: CURRENT STATUS: {modstatus}</label>
                                    </div>
                                    <div className="form-group mb-3">
                                    <label htmlFor="ktl_frame_price">Price: </label>
                                    {currencyFormater.format(price)} 
                                    </div>
                                   </div>
                               </div>
                            </div>
                       </div>
                   </div>
                   </div>
                </div>
            </div>

            <div className="row" style={{background:"#f5f5f5", padding:20}}>
            {currentPost.map((sample,key)=>(
                    <div className="col-md-6 p-0" id={`ktl-sample-${key}`} key={sample.id} style={{overflow:"hidden", border:"10px solid #f5f5f5"}}>
                        <div className="row info-box justify-content-center align-items-center" style={{overflow:"hidden", height:200}} 
                            onClick={(e)=>{
                                if (selected) {
                                    let firstSelected = document.querySelector(`#${selected}`)
                                    if (firstSelected) {
                                        firstSelected.style.borderColor = "#eee"
                                    }
                                }
                                    setSelected(`ktl-sample-${key}`);
                                    setJobExtra({jobExtra:" Frame with sample id: " + sample.id});
                                    setExtra(sample.price)
                                    setJobBill({frameCoverPrice:sample.price})
                                    let newSelect = document.querySelector(`#ktl-sample-${key}`)
                                    newSelect.style.borderColor = "#ff0080"
                            }}
                        >
                            <div className="col-md-12 d-flex justify-content-center" style={{overflow:"hidden"}}>
                            <img src={'/'+sample.image_url} alt="" style={{objectFit:"fill", maxWidth:200, maxHeight:200}}/>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 btn center" style={{background:"#fff", color:"#000", fontFamily:"arial black",}}  onClick={()=>{
                                setPreviewUrl(sample.image_url)
                                setModal('flex')
                                setPrice(sample.price)
                                console.log(sample.status, 'the real sample status ')
                                setFrameid(sample.id)
                                setStatus(sample.status)
                                setModstatus(sample.status)
                            }}>
                            <span className="p-2 border-info"> View</span>
                            </div>
                        </div>
                        
                        <div className="row bg-light shadow-lg justify-content-center align-items-center" style={{minHeight:50, borderTop:`5px solid #ccc`}}>
                            <div className="col-5">
                                <span className="fa fa-tag" style={{padding:10}}>
                                 {sample.id}
                                </span>
                            </div>
                            
                            <div className="col-5">
                                <span className="" style={{color:"#333",fontFamily:"arial black", padding:10}}>
                                  {currencyFormater.format(sample.price)} 
                                </span>
                            </div>
                        </div>
                    </div>
            ))}
            </div>
            <div className="col-md-12">
                <div className="row px-4">
                <Pagination postPerPage={postPerPage} setPage={setPage} totalPost={frames.length} />
                </div>
            </div>
        </div>
    )
}

export default Frames
