import React, { useState, useContext, useEffect, Fragment } from 'react'
import { AuthAlertContext } from '../../../AuthAlert/AuthAlertContext'

export const Anouncement = () => {
    const {setAlert} = useContext(AuthAlertContext)
    const [message, setmessage] = useState(null)
    const [modal, setModal] = useState('none')
    const [ announcement, setAnnouncement] = useState([])

    useEffect(() => {
        getAnnouncement()
    }, [])
    // url /admin/add-announcemnt
    const addAnouncement =  function(){
        if (message) {
            let dataform =  new FormData();
            dataform.append('message', message)
            let axhr =  new XMLHttpRequest();
            axhr.open('POST', '/admin/add-announcemnt', true)
            axhr.onload = function(){
                if (this.status == 200 && this.readyState == 4) {
                    if (this.responseText) {
                        if (JSON.parse(this.responseText)) {
                            if (JSON.parse(this.responseText).response) {
                                if (JSON.parse(this.responseText).response.state) {
                                    console.log(this.responseText)
                                    document.querySelectorAll('textarea').forEach(text=>text.value=null)
                                    getAnnouncement();
                                    setAlert(JSON.parse(axhr.responseText).response.message,'success')
                                    setModal('none');

                                }
                            }
                        }
                    }
                }
            }
            axhr.setRequestHeader('x-auth-token', localStorage.adminToken);
            axhr.send(dataform)
        }

    }

    const getAnnouncement = ()=>{
        let xhr = new XMLHttpRequest();
        xhr.open('GET', '/admin/get-announcement', true)
        xhr.onload = ()=>{
            if (xhr.readyState == 4 && xhr.status == 200) {
                if (xhr.responseText) {
                    if (JSON.parse(xhr.responseText)) {
                        console.log(xhr.responseText)
                        if (JSON.parse(xhr.responseText).response) {
                            if (JSON.parse(xhr.responseText).response.state) {
                                setAnnouncement((JSON.parse(xhr.responseText).response.result))
                                console.log(xhr.responseText, 'hello fool ')
                            }
                        }
                    }   
                }
            }
        }
        xhr.setRequestHeader('x-auth-token',localStorage.adminToken);
        xhr.send();
    }

    const deleteAnnouncement = (id)=>{
        if (id) {
            let dataform = new FormData();
            dataform.append('id', id)
            let delxhr =  new XMLHttpRequest()
            delxhr.open('POST','/admin/delete-announcement', true)
            delxhr.onload= ()=>{
                if (delxhr.readyState === 4 && delxhr.status===200) {
                    if (delxhr.responseText) {
                        if (JSON.parse(delxhr.responseText)) {
                            if (JSON.parse(delxhr.responseText).response) {
                                if (JSON.parse(delxhr.responseText).response.state) {
                                    setAlert(JSON.parse(delxhr.responseText).response.message, 'success')
                                    getAnnouncement()
                                }
                            }
                        }
                    }
                }
            }
            delxhr.setRequestHeader('x-auth-token',localStorage.adminToken);
            delxhr.send(dataform);
        }
    }

    return (
        <div className="col-md-12">

                          {/* modal starts here  */}
         <div className="row justify-content-center" style={{display:`${modal}`,overflowY:'auto', overflowX:"hidden",justifyContent:"center", alignItems:"center", position:"fixed",bottom:0, top:0,left:0,right:0, width:"100%", height:"100%", background:"linear-gradient(90deg, rgba(207, 207, 207,0.8), rgba(207, 207, 207,0.8))", zIndex:6}}>
         <div className="row justify-content-center" style={{}}>
            <div className="col-md-12 " style={{}}>
            <div className="mb-3 p-3 text-center text-danger" style={{borderRadius:50, position:"fixed", top:10, right:10, zIndex:5}} 
                    onClick={()=>{
                        setModal('none')
                    }}> 
                <span className={'fa fa-times'} style={{fontSize:50}}>
                </span>
                </div>
        {/* Support box starts here  */}

        <div className="row justify-content-center">
               <div className="col-md-4 mt-4" style={{background:"#fefefe", padding:30, borderRight:"20px solid #eee",  borderLeft:"20px solid #eee",}}>
                   <h2 className="p-3">Enter Anouncement</h2>
               <form>
                    <div className="form-group row">
                            <div className="col-12">
                            <textarea className="form-control" name="" id="" cols="30" rows="10"  onChange={(e)=>{
                                setmessage(e.target.value)
                            }}></textarea>
                        </div>
                    </div>
                    <div className="form-group row">
                            <div className="col-12">
                            <input type="button" value="Add Announcement" className="form-control btn btn-info"  placeholder="col-form-label" 
                            onClick={()=>addAnouncement()}
                            />
                        </div>
                    </div>
                </form>
               </div>
            </div>

        {/* Support box ends here  */}
        </div>
     </div>
     </div>
{/* modal ends here  */}


            <div className="timeline">
                <div onClick={()=>setModal('block')}>
                    <i className="fas fa-plus bg-green"></i>
                    <div className="timeline-item btn-primary">
                        <h3 className="timeline-header no-border"><a href="#">Add New Announcement</a></h3>
                    </div>
                </div>
                {announcement && announcement.map((note, key)=>(
                    <Fragment key={key}>
                        <div>
                        <i className="fas fa-info bg-blue"></i>
                        <div className="timeline-item">
                            <span className="time"><i className="fas fa-clock"></i> {note.created_on}</span>
                            <h3 className="timeline-header"><span className="text-primary">Date </span>  </h3>
                            <div className="timeline-body">
                            {note.message}
                                <div className="row">
                                    <div className="col-md-12 text-right">
                                    <i className="fas fa-trash text-danger" style={{cursor:"pointer"}} onClick={()=>{
                                       deleteAnnouncement(note.id)
                                    }}></i>
                                    </div>
                                </div>
                            </div>
                        </div>
                        </div>
                    </Fragment>
                    ))}
                </div>
        </div>
    )
}

export default Anouncement
