import React,{useReducer, useEffect, useContext} from 'react'
import AdminAuthReducer from './AdminAuthReducer'
import AdminAuthContext from './AdminAuthContext'
import axios from 'axios'
import setAdminToken from '../../utils/setAdminToken'
import { AuthAlertContext } from '../../AuthAlert/AuthAlertContext'


function AuthState(props) {
    const adminAlert = useContext(AuthAlertContext)
    const initialState = {
        notLoggedIn:false,
        authenticated:null,
        user:null,
        isLoading: true
    }

    const [state, dispatch] = useReducer(AdminAuthReducer, initialState);
    // Load and set users 
    const LoadUser=async ()=>{
        dispatch({type:'ISLOADING'})
        if (localStorage.adminToken) {
                setAdminToken(localStorage.adminToken)
                let res = await axios.get('/auth/admin')
                try {
                    if (res.data.response) {
                        if (res.data.response.data) {
                            dispatch({
                                type: 'LOAD_USER_SUCCESS', 
                                payload: {
                                    user: res.data.response.data
                                }
                            })
                        }
                    }
                } catch (error) {
                    dispatch({
                        type: 'LOAD_USER_FAILED', 
                        payload: {
                            user: null
                        }
                    })
                }
            }else{
                dispatch({
                    type: 'LOAD_USER_FAILED', 
                    payload: {
                        user: null
                    }
                })
            }

    }

    // Login
    const Login = (token) =>{
        if (token) {
            dispatch({type:'LOGIN', payload:{token: token}})
            LoadUser();
        }
    }


    // Register
    const Register = async (dataform) =>{
        const res = await axios.post('/register', dataform, {
            headers: {
                "Content-Type": "application/json"
            }})
            try {
                await dispatch({
                    type: 'REGISTER_SUCCESS',
                    payload: {token: res.data.token}
                    })
                await LoadUser()
                console.log(res,'the complete response ')
            } catch (error) {
                console.log(error, 'error occured while tryna register this user ')
            }
    }

    // Logout 
    const Logout = async()=>{
        dispatch({type: 'LOGOUT'})

    }

    //Authenticate 
    const Authenticate = async ()=>{
        dispatch({
            kind:'authenticate',
            payload:{
            user: JSON.parse(localStorage.getItem("user")),
           }
         })
    }


    return (
        <AdminAuthContext.Provider 
            value={{
                Login,
                Authenticate,
                state,
                Register,
                LoadUser,
                Logout,
                init: state,
                isAuthenticated:state.authenticated,
                isLoading: state.isLoading,
                notLoggedIn:state.notLoggedIn
                
            }}
        >

            {props.children}
        </AdminAuthContext.Provider>
    )
}

export default AuthState

