import React, {useContext, useState, useEffect} from 'react'

export const UserPanel = () => {
    const [uncompletedJobs, SetUncompletedJobs] = useState([]);
    const [draftJobs, setDraftJobs] = useState([]);
    const [completdJobs, SetCompletedJobs] = useState([]);
    const [lastBooked, setLastBooked] = useState(null);
    const [userJobs, setUserJobs] = useState([]);

    useEffect(() => {
        getJobs();
    }, [])

    const getJobs =()=>{
        let xhr  = new XMLHttpRequest()
        xhr.open('GET', '/ktl-jobs-status', true)
        xhr.onload = function(){
            if (xhr.readyState == 4, xhr.status ==200) {
                if (JSON.parse(xhr.responseText)) {
                    if (JSON.parse(xhr.responseText).response) {
                        if (JSON.parse(xhr.responseText).response.state) {                    
                            let jobs = JSON.parse(xhr.responseText).response.result
                            if (jobs) {
                                setUserJobs(jobs);
                                jobs.map((job,key)=>{
                                    job.job_status === 'draft' && setDraftJobs([...draftJobs, job])
                                    job.job_status === 'completed' && SetCompletedJobs([...completdJobs, job]) ;
                                    job.job_status !== 'completed' && SetUncompletedJobs([...uncompletedJobs, job]) ;
            
                                })
                            }
                        }
                    }
                }
                
            }
        }
        xhr.setRequestHeader('x-auth-token', localStorage.token)
        xhr.send();
    }

    return (
        <div className="col-md-6">
            <div className="row info-box">
                <div className="col-md-12">
                    <div className="small-box bg-info">
                    <div className="inner">
    <                   h3>{uncompletedJobs.length}</h3>
                        <p>Jobs Uncompleted</p>
                    </div>
                    </div>
                </div>
            </div>

            <div className="row info-box">
                <div className="col-md-12">
                <div className="small-box bg-primary">
                <div className="inner">
                    <h3>{completdJobs.length}<sup style={{fontSize:20}}></sup></h3>
                    <p>Completed jobs</p>
                </div>
                </div>
                </div>
            </div>
            <div className="row info-box">
                <div className="col-md-12">
                <div className="small-box bg-secondary">
                    <div className="inner">
                        <h3>{draftJobs.length}</h3>

                        <p>Drafted jobs</p>
                    </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default UserPanel
