import React, { Component, useState,useEffect, useContext, Fragment } from 'react'
import {AuthContext} from '../../../Auth/AuthState'

export const Profile = (props)=> {

    const {isAuthenticated, state} = useContext(AuthContext)

    const [profile, setProfile] = useState([])
    const [username, setUsername] = useState(null)

    useEffect(() => {
        getUser();
    }, [username])
    
    

    const getUser= async()=>{
        let xhr = new XMLHttpRequest();
        xhr.open('POST', `/profile`, true);
        xhr.onload =  async()=>{
            if (xhr.readyState == 4 && xhr.status == 200) {
                console.log(xhr.responseText)
                setProfile(JSON.parse(xhr.responseText));
            }
        }
        xhr.setRequestHeader('x-auth-token', localStorage.token)
        xhr.send();
    }

        return (
            <div className="col-md-12">
                <div className="row justify-content-center">


                    <div className="col-md-8">
                        <div className="card card-primary card-outline">
                            <div className="card-body box-profile">
                                <div className="text-center">
                                    <img className="profile-user-img img-fluid img-circle" src={profile.length > 0 && profile[0].user_image} style={{padding:3, height:100,objectFit:"cover"}} alt="User profile picture" />
                                </div>
                                <h3 className="profile-username text-center">{profile.length > 0 && profile[0].user_name}</h3>
                                <p className="text-muted text-center">KTL profile</p>
                                <ul className="list-group list-group-unbordered mb-3">
                                {profile.length >0 && profile.map((profile, key)=>(
                                    <Fragment key={key}>
                                    <li className="list-group-item"><b>Username</b> <a className="float-right">{profile.user_name}</a></li>
                                    <li className="list-group-item"><b>Email</b> <a className="float-right">{profile.user_email}</a></li>
                                    <li className="list-group-item"><b>Phone Number</b> <a className="float-right">{profile.user_phone}</a></li>
                                    <li className="list-group-item"><b>Address</b> <a className="float-right">{profile.user_address}</a></li>
                                    </Fragment>
                                ))}
                                </ul>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        )
}


export default Profile
