import React, { Component } from 'react'

export class EnterCopies extends Component {
    state={
        copies:0,
        bill:0
    }
    setValue(e){
       if ( this.setState({copies:parseInt(e.target.value)})) {
           console.log('the number of copies has been setted ')
       }
    }
    render() {
        return (
            <div className="col-md-7" style={{display:'flex', justifyContent:"center", alignItems:"center"}}>
                   <div className="row " style={{display:'flex', justifyContent:'center'}}>
                <div className="col-md-12" style={{background:"#628f9c", borderRadius:10, padding:20}}>
                    <form>
                      <div className="form-group" style={{display:'flex', minWidth:300, color:'#fff', textTransform:"uppercase", fontFamily:"arial black", background:"#587f8A", borderRadius:5, padding:20}}>
                                Enter number of copies
                            </div>
                        <div className="form-group" >
                            <input type="number" className="form-control" name="copies"
                              onChange={e=>this.setValue(e)}
                         />
                        </div>
                        <div className="form-group">
                        <input type="button" className="form-control border-0" value="Confirm Copies" style={{background:"#ff9900",fontFamily:"arial black",color:"#fff"}} name="confirmCopies" id="" onClick={()=>{
                           this.state.copies && this.props.setJobCopies({
                                jobCopies:this.state.copies,
                                jobBill:this.state.bill,
                                 currentView:"MakePayment"
                                 })
                        }}/>
                        </div>
                    </form>
                </div>
            </div>  
            </div>
        )
    }
}

export default EnterCopies
