import React, { Component } from 'react'


export class Pagination extends Component {

    state ={
        pageNumbers:[]
    }

    componentDidMount(){
    }
    
    Pagination(){
        console.log(this.props.totalPost, ' this sis the total post ')
        let pageNumbers = []
        // const {postPerPage, totalPost} = this.props
        let postPerPage = 5
        let totalPost = 100;
        for (let i = 0; i <= (totalPost/postPerPage); i++) {
            pageNumbers.push(i)
        }

        this.setState({pageNumbers})
    }
    render() {
        let pageNumbers = []
        for (let i = 1; i <= (Math.ceil(this.props.totalPost/this.props.postPerPage)); i++) {
            pageNumbers.push(i)
        }
        
        return (
            <div className="row justify-content-center p-1 mb-4" style={{background:"#f5f5f5"}}>
                <div className="col-md-12">
                    <div className="row mx-auto">
                        <nav aria-label="Page navigation ">
                        <ul className="pagination border-0">
                            {
                                pageNumbers.map(number=>(
                                    <li key={number} className="page-item page-link border-0" onClick={()=>this.props.setPage(number)} >{number}</li>
                                ))
                            }
                        </ul>
                        </nav>
                    </div>
                </div>
        </div>
        )
    }
}

export default Pagination
