export default (state, action)=>{

    switch (action.type) {
        case 'REGISTER_SUCCESS':
            localStorage.setItem('adminToken', action.payload.token)
            return {
                ...state,
                ...action.payload,
                authenticated: true
            }
        case 'REGISTER_FAILED':
        case 'LOGOUT':
            localStorage.removeItem('adminToken')
            return {
                ...state,
                user: null,
                token:null,
                isLoading:true,
                notLoggedIn:true,
                authenticated: false,
            }

        case 'LOAD_USER_SUCCESS':
            return {
                ...state,
                ...action.payload,
                authenticated: true,
                isLoading:false,
            }
            case 'ISLOADING':
                return {
                    ...state,
                    isLoading:true,
                }
        case 'LOAD_USER_FAILED':
            return {
                ...state,
                ...action.payload,
                isLoading:false,
                authenticated: false,
                isLoggedIn: false
            }
    

        case 'authenticate':
            return {
                ...state,
                authenticated: true,
                // ...action.payload
            }    
        case 'LOGIN':
            localStorage.setItem('adminToken', action.payload.token)
            return {
                ...state,
                ...action.payload,
                authenticated: true,
            }    
        default:
            return state;
    }
}