import React, { Component } from 'react'
import {Redirect} from 'react-router-dom'
import {AuthContext} from '../Auth/AuthState'

export class Register extends Component {
    state ={
        username: null,
        password: null,
        email: null,
        address: null,
        phone: null,
        confirmPasssword:null,
        profileImage: null,
        profileFile: null
    }
    
    componentDidMount(){
        this.context.LoadUser();
    }

    registerUser(){
        const {Register} = this.context
        const {username, password, email, phone, address, profileImage,profileFile} = this.state
        if (username) {
            if (password) {
                if (email) {
                    if (phone) {
                        if (profileImage && profileFile) {
                            let dataForm = new FormData()
                            dataForm.append('username', username)
                            dataForm.append('email', email)
                            dataForm.append('password', password)
                            dataForm.append('phone', phone)
                            dataForm.append('address', address)
                            dataForm.append('file', profileFile, profileFile.name)
                            let regXHR = new XMLHttpRequest();
                            regXHR.open('POST', '/register', true)
                            regXHR.onload =  ()=>{
                                if (regXHR.readyState ===4 && regXHR.status===200) {
                                    if (regXHR.responseText) {
                                        if (JSON.parse(regXHR.responseText)) {
                                            if (JSON.parse(regXHR.responseText).response) {
                                                if (JSON.parse(regXHR.responseText).response.state) {
                                                    Register(JSON.parse(regXHR.responseText).response.token)
                                                } 
                                            }   
                                        }                                        
                                    }
                                }
                            }
                            regXHR.send(dataForm)
                            // Register({
                            //     username,
                            //     email,
                            //     password,
                            //     phone,
                            //     address,
                            //     profileImage
                            // })
                        }
                    }
                }
            }
        }

    }

    render() {
        return !this.context.isAuthenticated ? (
            <div className="container-fluid" style={{background:"#587f8A", height:"100vh"}}>
                <div className="row justify-content-center" style={{display:"flex", flex:1}}>
                    <div className="col-md-6" style={{background:"#f5f5f5", borderRadius:5,padding:20}}>
                        <div className="row p-4">
                            <h2 style={{color: "#faa200", textShadow:"#000 0 0 1px"}}>Register</h2>
                        </div>
                        <form>
                            <div className="form-group row justify-content-center">
                                <div className="col-md-4">
                                   {this.state.profileImage && ( <img src={this.state.profileImage} style={{width:80, height:80,objectFit:"cover",borderRadius:50}} alt="profile image "/>)}
                                </div>
                            </div>
                            <div className="form-group row">
                                <label htmlFor="profileImage" className="col-sm-4 col-form-label">Profile Image</label>
                                <div className="col-md-8">
                                <input type="file" className="form-control border-0" id="profileImage" onInput={async (e)=>{
                                const file = e.target.files[0];
                                if (file) {
                                    const type = /image\//.test(file.type)
                                    if (type) {
                                        this.setState({profileFile:file})
                                        const reader = new FileReader();                              
                                        reader.addEventListener("load", async  ()=> {
                                            await this.setState({profileImage:reader.result});
                                        }, false);
                                        if (file) {
                                            reader.readAsDataURL(file);
                                          }
                                    }
                                }
                            }} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label htmlFor="inputUsername" className="col-sm-4 col-form-label">Username</label>
                                <div className="col-sm-8">
                                <input type="text" className="form-control" name="username" id="inputUsername" placeholder="enter username" required onChange={(e)=>this.setState({username: e.target.value})}/>
                                </div>
                            </div>

                            <div className="form-group row">
                                <label htmlFor="inputEmail3" className="col-sm-4 col-form-label">Email Address</label>
                                <div className="col-sm-8">
                                <input type="email" className="form-control" id="inputEmail3" name="email" placeholder="enter email" required 
                                    onChange={(e)=>this.setState({email: e.target.value})}
                                />
                                </div>
                            </div>

                            
                            <div className="form-group row">
                                <label htmlFor="inputNumber" className="col-sm-4 col-form-label">Phone number</label>
                                <div className="col-sm-8">
                                <input type="number" className="form-control" id="inputNumber" name="phone" placeholder="phone" required 
                                
                                onChange={(e)=>this.setState({phone: e.target.value})}
                                />
                                </div>
                            </div>

                            
                            <div className="form-group row">
                                <label htmlFor="inputAddress" className="col-sm-4 col-form-label">Studio address</label>
                                <div className="col-sm-8">
                                <input type="text" className="form-control" id="inputAddress" name="address" placeholder="enter address" required 
                                    onChange={(e)=>this.setState({address: e.target.value})}
                                />
                                </div>
                            </div>

                            <div className="form-group row">
                                <label htmlFor="inputPassword" className="col-sm-4 col-form-label">Choose password</label>
                                <div className="col-sm-8">
                                <input type="password" className="form-control" id="inputPassword" name="password" placeholder="password" required 
                                    onChange={(e)=>this.setState({password: e.target.value})}
                                />
                                </div>
                            </div>

                            <div className="form-group row">
                                <label htmlFor="inputConfirmPassword" className="col-sm-4 col-form-label">Confirm password</label>
                                <div className="col-sm-8">
                                <input type="password" className="form-control" id="inputConfirmPassword" name="confirmPassword" placeholder="confirm password" required 
                                    onChange={(e)=>this.setState({confirmPasssword: e.target.value})}
                                />
                                </div>
                            </div>

                            <div className="form-group row">
                                <div className="col-sm-8">
                                <input type="button" className="form-control my-4 p-0" value="Submit"   style={{background:"#2f6473", color: "#faa200", textShadow:"#000 0 0 1px", fontFamily:"arial black", textTransform:"uppercase"}}
                                    onClick={()=>this.registerUser()}
                                />
                                </div>
                            </div>
                            <div className="form-group row">
                                <div className="col-sm-8">
                                    <a href="!#" style={{textDecoration:"none", color:"#587f8A",}}>Already have an account, Login</a>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        ):<Redirect to='/dashboard' />
    }
}

Register.contextType = AuthContext
export default Register
