import React, { Component } from 'react'

export class Footer extends Component {
    render() {
        return (
            <div className="row" style={{bottom:0,}}>
                <div className="col-md-12" style={{display:"flex",background:"#f3f3f3", minHeight:30, padding:20,}}>
                    &copy; KingsTyme Photo Lab {new Date().getFullYear()}
                </div>
            </div>
        )
    }
}

export default Footer
