import React , {useEffect, useState} from 'react'
import Frames from './Frames'
import Covers from './Covers'

export  function JobExtra({setJobExtra, jobType,jobBill, setJobBill}) {
    const [extra, setExtra] = useState(null);

    const extrItems=()=>{
        if (jobType == "Enlargement") {
           return <Frames  setJobExtra={setJobExtra} setExtra={setExtra} jobBill={jobBill} setJobBill={setJobBill}/>

        }else if(jobType == "Photobook"){
            return <Covers setJobExtra={setJobExtra} setExtra={setExtra} jobBill={jobBill} setJobBill={setJobBill}/>
        }else if(jobType == "Potrait"){
            return setJobBill({currentView:'EnterCopies'})
        }
        
        
    }
    const validExtra = () =>{
      return  extra && <div className="row justify-content-center my-4">
            <div className="col-md-5 text-center" style={{borderRadius:10,background:"#587f8A", color:"#fff", borderBottom:"1px #ff0080 solid "}}  onClick={()=>setJobExtra({currentView: 'EnterCopies'})}>
                <h2> CONTINUE</h2>
            </div>
        </div>
    }
    return (
        <div className="col-md-7 mb-5">
            {extrItems()}
            {validExtra()}

        </div>
    )
    
}

