import React, { Component } from 'react'

export class ForgottenPassword extends Component {
    render() {
        return (
            <div className="container-fluid" style={{background:"#587f8A", height:"100vh"}}>
                <div className="row justify-content-center" style={{display:"flex", flex:1}}>
                    <div className="col-md-6" style={{background:"#f5f5f5", borderRadius:5,padding:20}}>
                        <div className="row p-4">
                            <h2 style={{color: "#faa200", textShadow:"#000 0 0 1px"}}>Forgotten Password</h2>
                        </div>
                        <form action="" method="post">

                            <div className="form-group row">
                                <label for="inputEmail3" className="col-sm-12 col-form-label">Enter your registered email  address </label>
                                <div className="col-sm-12">
                                <input type="email" className="form-control" id="inputEmail3" placeholder="Email" />
                                </div>
                            </div>

                            <div className="form-group row">
                                <div className="col-sm-8">
                                <input type="submit" className="form-control my-4 p-0" value="Submit" placeholder="confirm password"  style={{background:"#2f6473", color: "#faa200", textShadow:"#000 0 0 1px", fontFamily:"arial black", textTransform:"uppercase"}}/>
                                </div>
                            </div>

                            <div className="form-group row">
                                <div className="col-sm-8">
                                    <a href="#" style={{textDecoration:"none", color:"#587f8A",}}>Don't have an account, Register </a>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default ForgottenPassword
