import React, { useState,Fragment, useEffect, useContext } from 'react'
import {AuthContext} from '../../../Auth/AuthState'
import axios from 'axios'
import { AuthAlertContext } from '../../../AuthAlert/AuthAlertContext'

export const  Support = (props)=> {

    const {state,isAuthenticated, LoadUser, init} = useContext(AuthContext)
    const {} = state
    const {setAlert} = useContext(AuthAlertContext)
    const [message, setMessage] = useState('')
    const [userid, setUserid] = useState('')
    const [modal, setModal] = useState('none')
    const [ticket, setTicket] = useState(null)
    const [subject, setSubject] = useState('')
    const [supports, setSupport] = useState([])

    useEffect(() => {
        if (state) {
            if (state.user) {
                if (state.user.user_name) {
                    setUserid(state.user.user_id)
                    setTicket(state.user.user_id+Date.now())
                    getSupport();
                }
            }
        }
        
    }, [state, userid])


    const getSupport = async () =>{
        const res = await axios.get('/ktl-get-support')
        try {
            if (res.data) {
                if (res.data.response) {
                    if (res.data.response.state) {
                        setSupport(res.data.response.result)
                        // console.log(res.data.response.result)
                    }
                }
            }
        } catch (error) {
            console.log(error)
        }
    }
    const sendSupport = async ()=>{
        if (subject) {
            if (message) {
                console.log(userid, 'ok the user id is here ')
                if (userid) {
                    let dataForm = new FormData()
                    dataForm.append('subject', subject)
                    dataForm.append('message', message)
                    dataForm.append('user_id', userid)
                    dataForm.append('ticket', ticket)
                    console.log('hello before sending ')
                    console.log(ticket,'hello before sending ')
                    let res = await axios.post('/ktl-send-support', {
                        ticket, 
                        subject,
                        message,
                    })
                    try {
                        console.log(res)
                        if (res.data) {
                            if (res.data.response) {
                                if (res.data.response.state) {
                                    // setAlert(res.data.response.message, 'success');
                                    setModal('none')
                                    getSupport();
                                    setAlert(res.data.response.message, 'success')
                                }
                            }
                        }
                    } catch (error) {
                        console.log(error, 'this sis the axios error')
                    }
                }
            }
        }
    }
            

    return (
        <div className="col-md-12">
            {/* modal starts here  */}
         <div className="row" style={{display:`${modal}`,overflowY:'auto', overflowX:"hidden",justifyContent:"center", alignItems:"center", position:"fixed",bottom:0, top:0,left:0,right:0, width:"100%", height:"100%", background:"linear-gradient(90deg, rgba(207, 207, 207,0.8), rgba(207, 207, 207,0.8))", zIndex:6}}>
         <div className="row" style={{}}>
            <div className="col-md-12 " style={{}}>
                <div className="mb-3 p-3 text-center text-danger" style={{borderRadius:50, position:"fixed", top:10, right:10, zIndex:5}} 
                    onClick={()=>{
                        setModal('none')
                        console.log('you just clicked me ')
                    }}> 
                <span className={'fa fa-times'} style={{fontSize:50}}>
                </span>
                </div>
{/* Support box starts here  */}
            <div className="row justify-content-center">
                <div className="col-md-6 my-5" style={{background:"#fff", borderRadius:4,padding:20}}>
                <div className="row">
                    <div className="col-md-12" >
                        <h2 className="lead p-3">KTL Customer Support </h2>
                    </div>
                    <div className="col-md-12">
                    <form>
                        <div className="card card-info border-0">
                            <div className="card-header">
                                <h2 className="card-title">Tell us what you want </h2>
                            </div>
                            <div className="card-body border-0">
                            <div className="form-group">
                            <label htmlFor="">Subject</label>
                            <input type="text" name="subject" className="form-control" 
                                onChange={(e)=>setSubject(e.target.value)}
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="">Message</label>
                            <textarea className="form-control" name="message" id="" cols="30" rows="10" 
                                onChange={(e)=>setMessage(e.target.value)}
                            ></textarea>
                        </div>
                        <div className="form-group">
                            <input type="button" value="Submit Support " className="form-control" style={{background:"#587f8A", color:"#fff", textTransform:"uppercase"}} onClick={sendSupport}/>
                        </div>
                            </div>
                        </div>
                    </form>
                    </div>
                </div>
                </div>
            </div>
{/* Support box ends here  */}
         </div>
     </div>
     </div>
{/* modal ends here  */}
            <div className="row justify-content-center mb-4 btn-light" >
                <div className="col-md-8 col-sm-6 col-12">
                    <div className="info-box mt-3">
                        <span className="info-box-icon bg-info"><i className="fas fa-envelope"></i></span>
                        <div className="info-box-content">
                        <button type="button" className="btn btn-block btn btn-lg" onClick={()=>setModal('block')}>
                            <h2>Send Support </h2>
                        </button>
                        </div>
                    </div>
                </div>
            </div>   

            <div className="row">
            <div className="col-md-12">
                <div className="timeline">
                {supports && supports.map((msg, key)=>(
                    <Fragment key={key}>
                        <div>
                        <i className="fas fa-envelope bg-blue"></i>
                        <div className="timeline-item">
                            <span className="time"><i className="fas fa-clock"></i> {msg.created_on}</span>
                          <h3 className="timeline-header"><span className="text-secondary"><strong><i>Subject:</i> {msg.subject}</strong>  </span> <small>ticket: {msg.ticket}</small>  </h3>
                            <div className="timeline-body">
                            <strong>Message: </strong>{msg.message}
                            </div>
                            {msg.reply && ( <div className="timeline m-4">
                                <div>
                                <i className="fas fa-reply bg-secondary"></i>
                                <div className="timeline-item">
                                    <p className="timeline-body no-border">{msg.reply} <small className="text-right">{msg.reply_date}</small></p>
                                    <p className="timeline-body no-border"><small className="text-right"><strong>Replied Date: </strong>{msg.reply_date}</small></p>
                                </div>
                            </div>
                            </div>)}
                        </div>
                        </div>
                    </Fragment>
                    ))}

                </div>
            </div>
        </div>  
        </div>
    )
}

export default Support
