import React, { Component } from 'react'

export class SelectSize extends Component {
    state = {
        sizes:[]
    }

    setJobSize(size){
        this.props.setJobSize({jobSize:size.size ,extraPrice:size.extra_price,pricePerCopy:size.price, currentView:'JobExtra'})
    }

    currencyFormater = new Intl.NumberFormat('en-NG', {
        style: 'currency',
        currency: 'NGN',
      })

    setSize(){
        let sizeApi = null
        switch (this.props.jobType) {
            case "Enlargement":
                sizeApi = 'ktl-enlargement-sizes';
                break;
            case "Photobook":
                sizeApi = 'ktl-photobook-sizes';
                break;
            case "Potrait":
                sizeApi = 'ktl-potrait-sizes';
                break;
        
            default:
                break;
        }
        let xhr = new XMLHttpRequest();
        xhr.open('GET', `/${sizeApi}`, true)
        xhr.onload =  () => {
            if (xhr.readyState == 4 && xhr.status == 200) {
                this.setState({sizes:JSON.parse(xhr.responseText)})
            }
        }
        xhr.setRequestHeader('x-auth-token', localStorage.token)

        xhr.send();
    }

    componentDidMount(){

        this.setSize();
    }
    render() {
        return (
                <div className="col-md-7 mx-auto p-3" sstyle={{ borderRadius:20}}>

                    <div className="row p-3" style={{background:"#eee"}}>
                        <div className="col-md-12">
                            <div className="row bg-white">
                                <h2 className="lead p-4">
                                    Select {this.props.jobType} Size 
                                </h2>
                            </div>
                        </div>
                    </div>
                    {
                        this.state.sizes.map((size, key)=>(
                            <div key={key} className="row px-3" style={{background:"#f3f3f3"}}>
                               <div className="col-12">
                               <div className="info-box">
                                    <span className="info-box-icon bg-info" style={{minWidth:200}}>{this.currencyFormater.format(size.price)}</span>
                                    <div className="info-box-content">
                                    <button onClick={()=>this.setJobSize(size)} type="button" className="btn btn-block btn btn-lg">
                                        <h2>{size.size}</h2>
                                    </button>
                                    </div>
                                </div>
                               </div>
                            </div>
                        ))
                    }
                </div>
        )
    }
}

export default SelectSize
