import React, { Component, useContext, useEffect, useState } from 'react'
import {AuthContext} from '../Auth/AuthState'
import AuthAlert from '../AuthAlert/AuthAlert'
import {AuthAlertContext} from '../AuthAlert/AuthAlertContext'


export const Login = (props) =>{
    const [username, setUsername] = useState(null)
    const [password, setPassword] = useState(null)
    const alertCxt = useContext(AuthAlertContext)
    const authContext = useContext(AuthContext)
    const {isAuthenticated, Login, LoadUser, state} = authContext
    useEffect(() => {
        LoadUser();
        if (state) {
             if  (isAuthenticated) {
                props.history.push('/dashboard')
            }
        }
    }, [props.history, isAuthenticated])

    const login = () =>{
        if (username && password) {
            let dataform = new FormData()
            dataform.append('username', username)
            dataform.append('password', password)
            let lxhr =  new XMLHttpRequest()
            lxhr.open("POST", "/login", true)
            lxhr.onload = ()=>{
                if (lxhr.readyState ==4  && lxhr.status ==200) {
                    if (lxhr.responseText) {
                        if (JSON.parse(lxhr.responseText)) {
                            console.log(lxhr.responseText, 'my client response text ')
                            if (JSON.parse(lxhr.responseText).response) {
                                if (JSON.parse(lxhr.responseText).response.state) {
                                    if (JSON.parse(lxhr.responseText).response.token) {
                                        let data = JSON.parse(lxhr.responseText)
                                        console.log(data, 'data from the auth state ')
                                        Login(data.response.token)
                                    }
                                }else{
                                    alertCxt.setAlert(JSON.parse(lxhr.responseText).response.message, 'danger')
                                }
                            }else{
                                alertCxt.setAlert('Invalid credentials', 'danger')
                            }
                        }else{
                            alertCxt.setAlert('Invalid credentials', 'danger')
                        }
                    }else{
                        alertCxt.setAlert('Invalid credentials', 'danger')
                    }
                }else{
                    alertCxt.setAlert('Invalid credentials', 'danger')
                }
            }
            lxhr.send(dataform)
        }else{
            alertCxt.setAlert('Please fill all fields', 'danger')
        }
    }
    return (
        <div className="container-fluid" style={{background:"#587f8A", minHeight:"100vh"}}>
        <div className="row justify-content-center">
            <div className="col-md-6 mt-5" style={{background:"#f5f5f5", borderRadius:5,padding:20}}>
                <AuthAlert />
                <div className="row p-4">
                    <h2 style={{color: "#faa200", textShadow:"#000 0 0 1px"}}>Login</h2>
                </div>
                <form>

                    <div className="form-group row">
                        <label htmlFor="inputEmail3" className="col-sm-4 col-form-label">Username</label>
                        <div className="col-sm-8">
                        <input onChange= {(e=>setUsername(e.target.value))} type="text" className="form-control" id="inputEmail3" name="username" placeholder="username" />
                        </div>
                    </div>

                    <div className="form-group row">
                        <label htmlFor="inputEmail3" className="col-sm-4 col-form-label">Password</label>
                        <div className="col-sm-8">
                        <input onChange= {(e=>setPassword(e.target.value))} type="password" className="form-control" name="password" id="inputEmail3" placeholder="enter password" />
                        </div>
                    </div>

                    <div className="form-group row">
                        <div className="col-sm-8">
                        <input type="button" onClick={(e)=>{
                            login()
                        }} 
                        className="form-control my-4 p-0" value="Submit" placeholder="confirm password"  style={{background:"#2f6473", color: "#faa200", textShadow:"#000 0 0 1px", fontFamily:"arial black", textTransform:"uppercase"}}/>
                        </div>
                    </div>

                    <div className="form-group row">
                        <div className="col-sm-8">
                            <a href="#" style={{textDecoration:"none", color:"#587f8A",}}>Forgotten password</a>
                        </div>
                    </div>
                    <div className="form-group row">
                        <div className="col-sm-8">
                            <a href="#" style={{textDecoration:"none", color:"#587f8A",}}>Don't have an account ? register </a>
                        </div>
                    </div>
                </form>
            </div>

        </div>
        <div className="row justify-content-center">
        <div className="col-md-6 text-white text-center  mt-5">
                <b>
                    <strong style={{textShadow:"0 10px 10px 0 #000"}}>
                    KingsTyme Photo Lab &copy; {new Date().getFullYear()}
                    </strong>
                </b>
            </div>
        </div>
    </div>
    )
}


export default Login
