import React, { Fragment,useState,useEffect, useContext } from 'react'
import {AuthAlertContext} from '../../../AuthAlert/AuthAlertContext'
import Axios from 'axios'

export const ReplySupport = () => {
    const {setAlert} = useContext(AuthAlertContext)

    const [supports, setSupports] = useState([])
    const [replySupports, setReplySupports] = useState([])
    const [userId, setUserId] = useState(null)
    const [userName, setUserName] = useState(null)
    const [ticket, setTicket] = useState(null)
    const [replyingTo, setReplyingTo] = useState(null)
    const [message, setMessage] = useState(null)
    const [modal, setModal] = useState('none')
    useEffect(() => {
        getSupports()
    }, [])

    const getSupports= async()=>{
        const res = await Axios.get('/admin/get-support');
        try {
            if (res.data) {
                console.log(res, 'ther reply server response ')
                if (res.data.response) {
                    if (res.data.response.state) {
                        let resData =  res.data.response.result
                        setSupports(res.data.response.result)
                    }
                }
            }
        } catch (error) {
            
        }
    }

    const replySupport= async()=>{
        if (message) {
            console.log(message, 'message setted ')
            if (userId) {
                console.log(userId, 'userid setted ')
                if (userName) {
                    console.log(userName, 'username setted ')
                    
                    if (ticket) {
                        console.log(ticket, 'ticket setted ')
                        const res = await Axios.post('/admin/reply-support', {
                            message,
                            userId, 
                            userName,
                            ticket
                        });
                        try {
                            console.log(res);
                            if (res.data) {
                                if (res.data.response) {
                                    if (res.data.response.state) {
                                        let resData =  res.data.response.message
                                        setModal('none')
                                        setAlert(res.data.response.message, 'success')
                                        document.querySelector('textarea').value = ''
                                    }
                                }
                            }
                        } catch (error) {
                            
                        }
                    }
                }
            }
        }
    }

    return (
        <div className="col-md-12">
              {/* modal starts here  */}
         <div className="row justify-content-center" style={{display:`${modal}`,overflowY:'auto', overflowX:"hidden",justifyContent:"center", alignItems:"center", position:"fixed",bottom:0, top:0,left:0,right:0, width:"100%", height:"100%", background:"linear-gradient(90deg, rgba(207, 207, 207,0.8), rgba(207, 207, 207,0.8))", zIndex:6}}>
         <div className="row justify-content-center" style={{}}>
            <div className="col-md-12 " style={{}}>
            <div className="mb-3 p-3 text-center text-danger" style={{borderRadius:50, position:"fixed", top:10, right:10, zIndex:5}} 
                    onClick={()=>{
                        setModal('none')
                        console.log('you just clicked me ')
                    }}> 
                <span className={'fa fa-times'} style={{fontSize:50}}>
                </span>
                </div>

            
            <div className="row justify-content-center">
               <div className="col-md-6" style={{background:"#fefefe", padding:30, borderRight:"20px solid #eee",  borderLeft:"5px solid #fff"}}>
                <h2 className="lead row">Reply support from user: {userName}</h2>
                <h2 className="lead row">Customer's User ID: {userId}</h2> 
                <h2 className="lead row">Support Ticket: {ticket}</h2>
                <p className="lead row">Replying to: <br/></p><p>
                 <i><strong>{replyingTo}</strong></i></p>
               <form >
                    <div className="form-group row">
                    </div>
                    <div className="form-group row">
                        <label htmlFor="colFormLabel">Enter message </label>
                            <div className="col-12">
                            <textarea className="form-control" name="" id="" cols="30" rows="10" onChange={(e)=>{
                                setMessage(e.target.value)
                            }}></textarea>
                        </div>
                    </div>
                    <div className="form-group row">
                            <div className="col-12">
                            <input type="button" onClick={replySupport} value="Send Support" className="form-control btn btn-info"  placeholder="col-form-label" />
                        </div>
                    </div>
                </form>
               </div>
            </div>

{/* Support box ends here  */}
</div>
     </div>
     </div>
{/* modal ends here  */}

            <div className="row">
            <div className="col-md-12">
                {supports && supports.map((msg, key)=>(
                    <div className="timeline " style={{background:"#eee",borderRadius:10}}>
                    <Fragment key={key}>
                        <div>
                            <i className="fas fa-user bg-green"></i>
                            <div className="timeline-item">
                                <h3 className="timeline-header no-border"><a href="#">{msg.user_name}</a> {msg.subject}</h3>
                            </div>
                        </div>
                        <div>
                        <i className="fas fa-envelope bg-blue"></i>
                        <div className="timeline-item">
                            <span className="time"><i className="fas fa-clock"></i> {msg.created_on}</span>
                          <h3 className="timeline-header"><span className="text-secondary"><strong><i>Subject:</i> {msg.subject}</strong>  </span> <small>ticket: {msg.ticket}</small>  </h3>
                            <div className="timeline-body">
                            <strong>Message: </strong>{msg.message}
                                <div className="row">
                                    <div className="col-md-12 text-right">
                                    <i className="fas fa-reply text-primary" onClick={()=>{
                                       setUserId(msg.user_id)
                                       setUserName(msg.user_name)
                                       setTicket(msg.ticket) 
                                       setReplyingTo(msg.message) 
                                       setModal('block')
                                    }}></i>
                                    </div>
                                </div>
                            </div>
                            {msg.reply && ( <div className="timeline m-4">
                                <div>
                                <i className="fas fa-reply bg-secondary"></i>
                                <div className="timeline-item">
                                    <p className="timeline-body no-border">{msg.reply} <small className="text-right">{msg.reply_date}</small></p>
                                    <p className="timeline-body no-border"><small className="text-right"><strong>Replied Date: </strong>{msg.reply_date}</small></p>
                                </div>
                            </div>
                            </div>)}
                        </div>
                        </div>
                    </Fragment>
                    </div>
                    ))}
            </div>
        </div>  

        </div>
    )
}

export default ReplySupport
