import React, { useEffect, useState, useContext, Fragment } from 'react'
import { AuthAlertContext } from '../../../AuthAlert/AuthAlertContext';

export const UsersJobStatus=()=> {
    const {setAlert} = useContext(AuthAlertContext)
    const [jobid, setJobid] = useState(null);
    const [userJobs, setUserJobs] = useState([]);
    const [status, setStatus] = useState('completed')
    const [downloadLink, setDownloadLink] = useState(null)
    const [modal, setModal] = useState('none')

    const [currentPage, setCurrentPage] = useState(1)
    const [postPerPage, setPostPerPage] = useState(10)

        useEffect(() => {
            getJobs();
        }, [])

        const getJobs =() => {
            let xhr  = new XMLHttpRequest()
            xhr.open('GET', '/ktl-admin-jobs-status', true)
            xhr.onload = ()=>{
                if (xhr.readyState == 4, xhr.status ==200) {
                    setUserJobs(JSON.parse(xhr.responseText))
                    console.log(xhr.responseText,'All user jobs ');
                }
            }
    
            xhr.setRequestHeader('x-auth-token', localStorage.adminToken)
            xhr.send();
        }
        const markJobComplete=()=>{
            if (jobid) {
               if (status) {
                let dataform = new FormData();
                dataform.append('id', jobid)
                dataform.append('status', status)
                let jsxhr = new XMLHttpRequest();
                jsxhr.open('POST', '/admin/modify-job-status', true)
                jsxhr.onload = ()=>{
                    if (jsxhr.readyState ==4 && jsxhr.status) {
                        if (jsxhr.responseText) {
                            console.log(jsxhr.responseText, 'mark as completed ')
                            if (JSON.parse(jsxhr.responseText)) {
                                if (JSON.parse(jsxhr.responseText).response) {
                                    if (JSON.parse(jsxhr.responseText).response.state) {
                                        setAlert(JSON.parse(jsxhr.responseText).response.message,'success')
                                        getJobs()
                                    }
                                }
                            }   
                        }
                    }
                }
                jsxhr.setRequestHeader('x-auth-token', localStorage.adminToken)
                jsxhr.send(dataform);
               }
            }
        }
        const grantUploadPermision=()=>{
            if (jobid) {
               if (status) {
                let dataform = new FormData();
                dataform.append('id', jobid)
                dataform.append('status', status)
                let upxhr = new XMLHttpRequest();
                upxhr.open('POST', '/admin/grant-upload-permision', true)
                upxhr.onload = ()=>{
                    if (upxhr.readyState ==4 && upxhr.status) {
                        if (upxhr.responseText) {
                            console.log(upxhr.responseText, 'mark as completed ')
                            if (JSON.parse(upxhr.responseText)) {
                                if (JSON.parse(upxhr.responseText).response) {
                                    if (JSON.parse(upxhr.responseText).response.state) {
                                        setAlert(JSON.parse(upxhr.responseText).response.message,'success')
                                        getJobs()
                                    }
                                }
                            }   
                        }
                    }
                }
                upxhr.setRequestHeader('x-auth-token', localStorage.adminToken)
                upxhr.send(dataform);
               }
            }
        }

        const dateFormater = new Intl.DateTimeFormat();
        const currencyFormater = new Intl.NumberFormat('en-NG', {
            style: 'currency',
            currency: 'NGN',
          })

        const setPage = (number)=>{
            setCurrentPage(number)
        }
        
        const indexOfLastPost = currentPage * postPerPage;
        const indexOfFirstPost = (indexOfLastPost - postPerPage);
        const currentPost = userJobs.length > 0 ? userJobs.slice(indexOfFirstPost, indexOfLastPost): [];
        
        return (
            <div className="col-md-12 p-4 bg-light" style={{overflowX:'auto'}}>

            <div className="row" style={{display:`${modal}`,overflowY:'auto', overflowX:"hidden",justifyContent:"center", alignItems:"center", flex:1, position:"fixed",bottom:0, top:0,left:0,right:0, width:"100%", height:"100%", background:"linear-gradient(90deg, rgba(207, 207, 207,0.8), rgba(207, 207, 207,0.8))", zIndex:6}}>
                    <div className="mb-3 p-3 text-center text-danger" style={{borderRadius:50, position:"fixed", top:10, right:10, zIndex:1}} 
                     onClick={()=>{
                         setModal('none')
                         console.log('you just clicked me ')
                      }}> 
                    <span className={'fa fa-times'} style={{fontSize:50}}>
                         </span>
                    </div>
                {/* modal */}
                <div className="col-md-3 col-sm-6 col-12">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="info-box" style={{cursor:'pointer'}} onClick={()=>{
                                grantUploadPermision()
                                setModal('none')
                            }}>
                                <span className="info-box-icon bg-danger"><i className="fas fa-thumbs-up"></i></span>
                                <div className="info-box-content">
                                    <span className="info-box-number">Grant Upload Permision </span>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="info-box" style={{cursor:'pointer'}} onClick={()=>{
                                markJobComplete()
                                setModal('none')
                            }}>
                                <span className="info-box-icon bg-success"><i className="fas fa-check"></i></span>
                                <div className="info-box-content">
                                    <span className="info-box-number">Mark As Completed </span>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="info-box" style={{cursor:'pointer'}}>
                                <span className="info-box-icon bg-warning"><i className="fas fa-download"></i></span>
                                <div className="info-box-content">
                                    <span className="info-box-number">Download </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>

<           section className="content">
            <div className="card" style={{overflowX:'hidden'}}>
                <div className="card-header">
                <h3 className="card-title">Your Jobs Status </h3>
                </div>
                <div className="card-body p-0" style={{display: 'block'}} style={{overflowX:'scroll'}}>
                <table className="table table-striped projects" >
                    <thead>
                        <tr>
                            <th scope="col">Job ID</th>
                            <th scope="col">Date Submited</th>
                            <th scope="col">Status</th>
                            <th scope="col">Status</th>
                            <th scope="col">Amount</th>
                            <th scope="col">Job Type</th>
                            <th scope="col">Specification</th>
                            <th scope="col">Copies</th>
                            <th scope="col">Size</th>
                            <th scope="col">Action</th>
                        </tr>
                    </thead>
                    <tbody>


                    {currentPost.map((job, key)=>(

                        <tr className="project-actions text-right" key={key} >
                            <th>{job.id}</th>
                            <td>{job.added_on}</td>
                            <td>
                                <b><strong>{job.job_status}</strong></b>
                            </td>
                            <td>{job.payment_status}</td>
                            <td>
                            <span style={{fontFamily:"arial black"}}>
                            {currencyFormater.format(job.bill)}
                                
                                </span>
                            </td>
                            <td>{job.job_type}</td>
                            <td>{job.job_extra}</td>
                            <td>{job.copies}</td>
                            <td>{job.job_size}</td>
                            <td>
                                <a className="btn btn-danger btn-sm text-white" onClick={()=>{
                                    setJobid(job.id)
                                    setDownloadLink(job.file)
                                    setModal('flex')
                                }}>
                                    <i className="fas fa-edit"></i>Modify
                                </a>
                            </td>
                        </tr>
                        ))}
                    </tbody>
                </table>
                </div>
                {/* <Pagination postPerPage={postPerPage} setPage={setPage} totalPost={userJobs.length} /> */}

            </div>

            </section>
            </div>
        )
}

export default UsersJobStatus
