export default (state, action)=>{
    switch (action.type) {
        case 'success':
        case 'danger':
        case 'info':
        case 'primary':
        case 'warning':
            return [...state, action]    
        case 'clearAlerts':
            return state.filter(alert=>alert.id !== action.id)
        default:
            return state
    }
}  