import React,  { useContext } from "react"
import {AuthAlertContext} from './AuthAlertContext'

const AuthAlert = () => {
const {state} = useContext(AuthAlertContext)
    return (
        <div className="col-md-12 px-4">
            {state && state.map((alert)=>(
                <div key={alert.id} className={`row my-2 p-3 bg-${alert.type} border text-center text-white `} style={{borderRadius:10}}>
                    <span className="fa fa-info-circle" style={{fontSize:30, maxHeight:10}}></span> <span> {' '+ alert.msg}</span> 
                </div>
            ))}
        </div>
    )
}

export default AuthAlert
