import React,{useReducer,createContext, useEffect} from 'react'
import {Redirect} from 'react-router-dom'
import PropTypes from 'prop-types'
import AuthReducer from './AuthReducer'
import axios from 'axios'
import setUserToken from '../utils/setUserToken'

export const AuthContext = createContext();
const { Provider } = AuthContext;

function AuthState({children}) {
    const iniialState = {
        notLoggedIn:null,
        authenticated:null,
        user:null,
        isLoading: true
    }

    const [state, dispatch] = useReducer(AuthReducer, iniialState);
    // Load and set users 

    const LoadUser=async ()=>{
        dispatch({type:'ISLOADING'})
        if (localStorage.token) {
            setUserToken(localStorage.token)
            let res = await axios.get('/auth/user')
            try {
                if (res.data.response) {
                    if (res.data.response.data) {
                        dispatch({
                            type: 'LOAD_USER_SUCCESS', 
                            payload: {
                                user: res.data.response.data
                            }
                        })
                    }
                }
            } catch (error) {
                dispatch({
                    type: 'LOAD_USER_FAILED', 
                    payload: {
                        user: null
                    }
                })
            }
        }else{
            dispatch({
                type: 'LOAD_USER_FAILED', 
                payload: {
                    user: null
                }
            })
        }
    }

    // Login
    const Login = (token) =>{
        dispatch({type:'LOGIN', payload:{token}})
        LoadUser();
    }


    // Register
    const Register = async (token) =>{
        try {
            dispatch({
                type: 'REGISTER_SUCCESS',
                payload: {token}
                })
            await LoadUser()
            console.log(token,'the complete response ')
        } catch (error) {
            console.log(error, 'error occured while tryna register this user ')
        }
    }

    // Logout 
    const Logout = ()=>{
        dispatch({type: 'LOGOUT'})
        // return <Redirect to="/login" />
    }

    //Authenticate 
    const Authenticate = async ()=>{
        dispatch({
            kind:'authenticate',
            payload:{
            user: JSON.parse(localStorage.getItem("user")),
           }
         })
    }


    return (
        <Provider 
            value={{
                Login,
                Authenticate,
                state,
                isAuthenticated:state.authenticated,
                isLoading: state.isLoading,
                notLoggedIn:state.notLoggedIn,
                Register,
                LoadUser,
                Logout,
                init: state
                
            }}
        >
            {children}
        </Provider>
    )
}

export default AuthState

