import React, { Component } from 'react'
import {useParams} from 'react-router-dom'
import { useState } from 'react'
import Axios from 'axios'
import { useEffect } from 'react'
import { Fragment } from 'react'

export const UploadJob = ()=> {
    const [fileName, setFileName] = useState('Add Zipped File ')
    const [file, setFile] = useState(null)
    const [job, setJob] = useState({})
    const [upload_endpoint, setUpload_endpoint] = useState(null)
    const [modal, setModal] =  useState('none')
    // const [reference, setReference] = useState(null)
    const ref = useParams()

    useEffect(() => {
        console.log(ref.reference, ' the motherfucking reference ')
        checkReference();
    }, [])

    const currencyFormater = new Intl.NumberFormat('en-NG', {
        style: 'currency',
        currency: 'NGN',
      })

    const checkReference = async()=>{
        let res = await Axios.post(`/dashboard/upload-job/${ref.reference}`);
        try {
            setJob(res.data.response.result[0]);
            setUpload_endpoint(res.data.response.result[0].upload_endpoint)
            console.log(res.data.response, ' ok ok ok ')
        } catch (error) {
            console.log(error, 'sorry an error has been encountered ')
        }
    }
        const uploadJob = () =>{
            if (file) {
                if (fileName) {
                    let dataForm = new FormData()
                    let uxhr = new XMLHttpRequest();
                    dataForm.append('reference', ref.reference)
                    dataForm.append('file', file)
                    uxhr.open('POST',upload_endpoint, true )
                    uxhr.onload = ()=>{
                        if (uxhr.readyState ===4 && uxhr.status ===200) {
                            console.log(uxhr.responseText, ' the upload response text ')
                            checkReference()
                            setModal('none')
                        }
                    }
                    uxhr.setRequestHeader('x-auth-token', localStorage.token)
                    uxhr.send(dataForm)

                }
            }
        }

        return job ?(
            <div className="col-md-10" style={{}}>
{/* modal starts here  */}
{upload_endpoint && (
         <div className="" style={{display:`${modal}`,overflowY:'auto', overflowX:"hidden",justifyContent:"center", alignItems:"center", flex:1, position:"fixed",bottom:0, top:0,left:0,right:0, width:"100%", height:"100%", background:"linear-gradient(90deg, rgba(207, 207, 207,0.8), rgba(207, 207, 207,0.8))", zIndex:6}}>
         <div className="" style={{display:"flex"}}>
            <div className="col-md-12 " style={{}}>
             
                    <div className="mb-3 p-3 text-center text-danger" style={{borderRadius:50, position:"fixed", top:10, right:10, zIndex:5}} 
                     onClick={()=>{
                         setModal('none')
                         console.log('you just clicked me ')
                      }}> 
                    <span className={'fa fa-times'} style={{fontSize:50}}>
                         </span>
                    </div>
                
{/* upload div starts here  */}


<div className="row mb-5 " style={{}}>
         <div className="col-md-12" style={{borderRadius:10, padding:20, background:"#628f9c",}}>
             <form>
                 <div className="form-group text-center " style={{}}>
                     <div className="text-center " style={{ minWidth:300, color:'#fff', textTransform:"uppercase", fontFamily:"arial black", background:"#587f8A", borderRadius:5, padding:20}} onClick={()=>{
                         document.querySelector("#uploadjob").click();
                     }}>
                         <span className="text-center">
                         {fileName}
                         </span>
                     </div>
                     <input type="file" className="form-control" name="file" style={{display:'none'}} id="uploadjob" onInput={(e)=>{
                         console.log(e.target.files, ' e.target files ')
                         let selectedFile = e.target.files
                         if (selectedFile) {
                             // setFile(selectedFile)
                             setFile(e.target.files[0])
                             if (e.target.files[0]) {
                                 if (e.target.files[0].name) {
                                     setFileName('Change: '+e.target.files[0].name)
                                 }
                             }
                         }
                     }}/>
                 </div>
                 <div className="form-group">
                 <input type="button" className="form-control border-0" value="Start Uploading" style={{background:"#ff9900",fontFamily:"arial black",color:"#fff"}} name="submitjob" id="" onClick={upload_endpoint && uploadJob}/>
                 </div>
             </form>
         </div>
     </div>  

{/* upload div ends her  */}
         </div>
     </div>
     </div>
)}

{/* modal ends here  */}

                <div className="row" style={{background:"#f5f5f", padding: 20, marginBottom:30, boxShadow: "0 2px 2px 0 #aaa"}}>
                    <div className="col-md-8">
                        <div className="row">
                            <div className="col-md-12">
                                <h2 className="lead p-3 mb-3" style={{fontFamily:"Arial black",}}>
                                    Job Details 
                                </h2>
                            </div>
                        </div>
                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                User ID: 
                            </div>
                            <div className="col-md-7">
                                {job.user_id}
                            </div>
                        </div>

                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                User Name: 
                            </div>
                            <div className="col-md-7">
                                {job.user_name}
                            </div>
                        </div>

                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                Payment Reference: 
                            </div>
                            <div className="col-md-7">
                                {job.reference}
                            </div>
                        </div>

                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                Job Id: 
                            </div>
                            <div className="col-md-7">
                                {job.id}
                            </div>
                        </div>
                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                Payment Status: 
                            </div>
                            <div className="col-md-7">
                                {job.payment_status}
                            </div>
                        </div>
                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                Payment Method : 
                            </div>
                            <div className="col-md-7">
                                {job.payment_method}
                            </div>
                        </div>

                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                Date Booked: 
                            </div>
                            <div className="col-md-7">
                                {job.added_on}
                            </div>
                        </div>
                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                Job Type: 
                            </div>
                            <div className="col-md-7">
                                {job.job_type}
                            </div>
                        </div>
                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                Job Size: 
                            </div>
                            <div className="col-md-7">
                                {job.job_size}
                            </div>
                        </div>
                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                Copies of job : 
                            </div>
                            <div className="col-md-7">
                                {job.copies}
                            </div>
                        </div>
                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                Amount Paid: 
                            </div>
                            <div className="col-md-7">
                            {currencyFormater.format(parseInt(job.bill))}
                            
                            </div>
                        </div>
                        <div className="row mb-2 p-2">
                            <div className="col-md-5">
                                Job Specification: 
                            </div>
                            <div className="col-md-7">
                                {job.job_extra}
                            </div>
                        </div>

                        <div className="row mb-2 p-2">
                            <div className="col-md-12 text-center btn btn-primary" style={{fontSize:14, fontFamily:"arial black"}} onClick={()=>{upload_endpoint && setModal('flex')}}>
                                <h2>
                                {upload_endpoint?'Send Your Job' : 'Your Job Has been Booked'}
                                </h2>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        ): (
            <Fragment>
                Sorry no such job 
            </Fragment>
        )
}

export default UploadJob
