import React, { useState, useEffect, Fragment, useContext } from 'react'
import Pagination from './Pagination'
import Cart from './Cart'
import Axios from 'axios'

export const MakePayment = ({jobState, setJobPayment,setPaymentMethod}) =>{
    
    const [modal, setModal] = useState("none") 
    useEffect(() => {
        let copies = parseInt(jobState.jobCopies)
        let extraprice = parseInt(jobState.extraPrice)
        let pricePerCopy = parseInt(jobState.pricePerCopy)
        let frameCoverPrice = parseInt(jobState.frameCoverPrice)
        let jobType = jobState.jobType
        if (jobType === 'Enlargement') {
            let price = (frameCoverPrice+pricePerCopy+extraprice) * copies
            setJobPayment({jobBill: price})
            // console.log(jobState.jobBill,'the job bill ')
            // console.log(`copies: ${copies}, extraprice:${extraprice}, pricepercopy:${pricePerCopy}, framecoverprice:${frameCoverPrice},jobType:${jobType}`)

        }else if (jobType === 'Photobook') {
            let price = ((copies*pricePerCopy) + extraprice + frameCoverPrice)
            setJobPayment({jobBill: price})
            // console.log(`copies: ${copies}, extraprice:${extraprice}, pricepercopy:${pricePerCopy}, framecoverprice:${frameCoverPrice},jobType:${jobType}`)
        }else if (jobType === 'Potrait') {
            let price = ((copies*pricePerCopy))
            setJobPayment({jobBill: price})
            // console.log(`copies: ${copies}, extraprice:${extraprice}, pricepercopy:${pricePerCopy}, framecoverprice:${frameCoverPrice},jobType:${jobType}`)
        }
    }, [])

    return (
        <div className="col-md-12  justify-content-center">
            <div className="row justify-content-enter">
            <div className="" style={{display:`${modal}`,overflowY:'auto', overflowX:"hidden",justifyContent:"center", alignItems:"center", flex:1, position:"fixed",bottom:0, top:0,left:0,right:0, width:"100%", height:"100%", background:"linear-gradient(90deg, rgba(207, 207, 207,0.8), rgba(207, 207, 207,0.8))", zIndex:6}}>
                {/* modal */}
            </div>
            <Cart jobState={jobState} payNow={true}  setPaymentMethod={setPaymentMethod}/>
            </div>
        </div>
    )
}

export default MakePayment


