import React, { Component, useEffect, useState } from 'react'
import { useContext } from 'react';
import {AuthContext} from '../../../Auth/AuthState'
import Axios from 'axios';

export const Cart = ({jobState, payNow, setPaymentMethod}) => {

const [user_name, setUser_name] = useState(null)
const [user_id, setUser_id] = useState(null)
const [user_phone, setUser_phone] = useState(null)
const {isAuthenticated, state} = useContext(AuthContext)


    useEffect(() => {
        setPaymentMethod &&setPaymentMethod({paymentMethod: 'offline'})
        if (state.user) {
            if (state.user.user_id) {
                setUser_id(state.user.user_id)
                setUser_name(state.user.user_name)
                setUser_phone(state.user.user_phone)
            }
        }
    }, [state.user])

    const currencyFormater = new Intl.NumberFormat('en-NG', {
        style: 'currency',
        currency: 'NGN',
      })

 const submitNewJob = ()=>{
        // user_name
        // editor
        // job_status
        // added_on
        // completed_on
        // payment_status
        // user_phone
        // user_id
        // payment_method
        // job_type
        // job_size
        // job_extra
        // copies
        // file
        // bill
    if (jobState.paymentMethod) {       
        let jobData = new FormData();
        jobData.append('user_name',user_name)
        jobData.append('job_status','draft')
        jobData.append('payment_method',jobState.paymentMethod)
        jobData.append('added_on',`${new Date()}`)
        jobData.append('payment_status','pending')
        jobData.append('user_phone', user_phone)
        jobData.append('user_id',user_id)
        jobData.append('job_type',jobState.jobType)
        jobData.append('job_size',jobState.jobSize)
        jobData.append('job_extra',jobState.jobExtra)
        jobData.append('copies',jobState.jobCopies)
        jobData.append('file',jobState.jobSelected)
        jobData.append('bill',jobState.jobBill)
        let sxhr = new XMLHttpRequest();
        sxhr.open("POST", '/submit-new-job', true)
        sxhr.onload  =async ()=>{
            if (sxhr.readyState == 4 && sxhr.status==200) {
                if (sxhr.responseText) {
                    if (JSON.parse(sxhr.responseText)) {
                        if (JSON.parse(sxhr.responseText).response) {
                            if (JSON.parse(sxhr.responseText).response.state) {
                                let resData = JSON.parse(sxhr.responseText).response.data
                                let ktl_ref = JSON.parse(sxhr.responseText).response.ktl_ref
                                let paymentDetail = JSON.parse(sxhr.responseText).response.detail
                                if (paymentDetail.offline_payment_detail) {
                                    window.location = '/dashboard/ktl-payment-detail'
                                }else if (paymentDetail.online_payment_detail) {
                                    let res = await Axios.post('/ktl-ref', {
                                        authorized_url: resData.data.authorization_url,
                                        reference: resData.data.reference,
                                        access_code: resData.data.access_code,
                                        ktl_ref
                                    })
                                    try {
                                        console.log(res.data, 'the second request ')
                                        window.location = resData.data.authorization_url
                                    } catch (error) {
                                        
                                    }   
                                }
                            }
                        }
                    }
                }
                console.log(sxhr.responseText)
            }
        }
        sxhr.setRequestHeader('x-auth-token', localStorage.token)
        sxhr.send(jobData);
     }
    }
    return (
            <div className="col-md-5">
                <ul className="list-group border-0 info-box"  style={{ borderRadius:5,  background:"#fff", border:"1px solid #f5f5f5",padding:20}}>
                    {jobState.jobType && <li className="list-group-item mx-0 border-0" style
                    ={{background:"#587f8A", color:"#fff"}}>Job Type: {jobState.jobType}</li>}
                    {jobState.jobSize&&<li className="list-group-item" style
                    ={{background:"#587f8A", color:"#fff"}}>Size: {jobState.jobSize} </li>}
                    {jobState.jobExtra&&<li className="list-group-item" style
                    ={{background:"#587f8A", color:"#fff"}}>Extra: {jobState.jobExtra}</li>}
                    {jobState.jobCopies&&<li className="list-group-item" style
                    ={{background:"#587f8A", color:"#fff"}}>Copies: {jobState.jobCopies}</li>}
                    {jobState.jobSelected&&<li className="list-group-item" style
                    ={{background:"#587f8A", color:"#fff"}}>File: {jobState.jobSelected}</li>}
                    {jobState.jobBill&&<li className="list-group-item" style
                    ={{background:"#587f8A", color:"#fff"}}>Bill: {currencyFormater.format(jobState.jobBill)}</li>}
                    {payNow &&
                        <li className="list-group-item" style={{background:"#587f8A", color:"#fff"}}>
                        <button className="btn btn-primary form-control" onClick={()=>submitNewJob()}>
                            Pay Online Now <i><small>(comming soon)</small></i>
                        </button>
                    </li>
                    }
                        {payNow &&
                        <li className="list-group-item" style={{background:"#587f8A", color:"#fff"}}>
                        <button className="btn btn-primary form-control" onClick={ submitNewJob}>
                            Pay Offline
                        </button>
                    </li>
                    }

                </ul>
            </div>
        )
}

export default Cart
