import React, { Component, useContext, useEffect, useState } from 'react'

import AdminAuthContext, {} from './AdminAuthContext'
import { AuthAlertContext } from '../../AuthAlert/AuthAlertContext'
import AdminAlert from '../../AuthAlert/AuthAlert'

const AdminLogin = (props) =>{

    const [username, setUsername] = useState(null)
    const [password, setPassword] = useState(null)
    const authContext = useContext(AdminAuthContext)
    const alertContext = useContext(AuthAlertContext)
    
    const {isAuthenticated, Authenticate, setUser, Login, LoadUser, state} = authContext
    useEffect(() => {
        LoadUser()
        if (state) {
             if  (state.authenticated) {
                 console.log('i can see this full ')
                props.history.push('/admin')
            }
        }
    }, [isAuthenticated,props.history, state.authenticated])

    const login = () =>{
        if (username && password) {
            let dataform = new FormData()
            dataform.append('adminname', username)
            dataform.append('password', password)
            let lxhr =  new XMLHttpRequest()
            lxhr.open("POST", "/admin/login", true)
            lxhr.onload = ()=>{
                if (lxhr.readyState ==4  && lxhr.status ==200) {
                    let data = JSON.parse(lxhr.responseText)
                    try {
                        if (data.response.state) {
                            if (data.response.token) {
                                console.log(data, 'data from the auth state ')

                                Login(data.response.token)
                            }
                        }else if(data.response.message) {
                            alertContext.setAlert(data.response.message, 'danger')
                        }                    
                    } catch (error) {
                        alertContext.setAlert('Invalid credentials', 'danger')
    
                    }
                }
            }
            lxhr.send(dataform)
        }else{
            alertContext.setAlert('Make sure to fill all fields ', 'danger')
        }
    }
    return  (
        <div className="container-fluid" style={{background:"#333",boxShadow:"#333 0 2px 2px 0 ", minHeight:"100vh"}}>
        <div className="row justify-content-center">
            <div className="col-md-4 mt-5" style={{background:"#f5f5f5", borderRadius:5,padding:20}}>
                <div className="row">
                <AdminAlert />
                </div>
                <div className="row p-4">
                    <h2 style={{color: "#faa200", textShadow:"#000 0 0 1px"}}>Admin</h2>
                </div>
                <form>
                    <div className="form-group row">
                        <label htmlFor="inputEmail3" className="col-sm-4 col-form-label">Username</label>
                        <div className="col-sm-8">
                        <input onChange= {(e=>setUsername(e.target.value))} type="text" className="form-control" id="inputEmail3" name="adminname" placeholder="username" />
                        </div>
                        </div>

                    <div className="form-group row">
                        <label htmlFor="inputEmail3" className="col-sm-4 col-form-label">Password</label>
                        <div className="col-sm-8">
                        <input onChange= {(e=>setPassword(e.target.value))} type="password" className="form-control" name="password" id="inputEmail3" placeholder="enter password" />
                        </div>
                    </div>

                    <div className="form-group row">
                        <div className="col-sm-8">
                        <input type="button" onClick={(e)=>{
                            login()
                        }} 
                        className="form-control my-4 p-0" value="Login" placeholder="confirm password"  style={{background:"#2f6473", color: "#faa200", textShadow:"#000 0 0 1px", fontFamily:"arial black", textTransform:"uppercase"}}/>
                        </div>
                    </div>
                </form>
            </div>

        </div>
        <div className="row justify-content-center">
        <div className="col-md-6 text-white text-center  mt-5">
                <b>
                    <strong style={{textShadow:"0 10px 10px 0 #000"}}>
                    KingsTyme Photo Lab &copy; {new Date().getFullYear()}
                    </strong>
                </b>
            </div>
        </div>
    </div>
    )
}


export default AdminLogin
