import React from 'react'
import { useState, useEffect } from 'react'
import Axios from 'axios'

export const Panel = () => {

    const [uncompletejobs, setUncompletejobs] = useState(0)
    const [jobsToday, setJobsToday] = useState(0)
    const [unrepliedMessages, setUnrepliedMessages] = useState(0)
    const [registeredUsers, setRegisteredUsers] = useState(0)

    
    useEffect(() => {
        adminStats()
    }, [])
    const adminStats = async () =>{
        const res = await Axios.get('/admin/stats');
        // numbers: {jobs: 10, users: 38, support: 6, jobsToday: 0}
        try {
            let result = res.data.response.numbers
            setUncompletejobs(result.jobs)
            setJobsToday(result.jobsToday)
            setUnrepliedMessages(result.support)
            setRegisteredUsers(result.users)
            console.log(res.data, 'the response ')
        } catch (error) {
            console.log(error, 'the errror')
        }
    }


    return (
        <div className="col-md-12" style={{borderRadius:20, padding:20}}>
            <div className="row justify-content-center">
                 <div className="col-md-3 m-2 text-white" style={{borderRadius:20,background:"#000261", overflow:"hidden", boxShadow:"0 4px 4px 0 #000"}}>
                    <div className="row align-items-center">
                        <div className="col-md-12 d-flex align-items-center justify-content-center" style={{minHeight:200}}>
                            <div className="row">
                                <div className="col-md-5 text-center">
                                    <h2>
                                        {uncompletejobs}
                                    </h2>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12 bg-info text-center p-2 text-white" style={{minHeight:50}}>
                            <span style={{textTransform:"uppercase", fontSize:15, textShadow:"1px 1px 0 #000",fontFamily:"arial black"}}>
                            Number of Uncompleted jobs 
                            </span>
                        </div>
                    </div>
                </div>

                {/* <div className="col-md-3 m-2 text-white" style={{borderRadius:20,background:"#ff9d00", overflow:"hidden", boxShadow:"0 4px 4px 0 #000"}}>
                    <div className="row align-items-center">
                        <div className="col-md-12 d-flex align-items-center justify-content-center" style={{minHeight:200}}>
                            <div className="row">
                                <div className="col-md-5 text-center">
                                    <h2>
                                        {jobsToday}
                                    </h2>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12 bg-info text-center p-2 text-white" style={{minHeight:50}}>
                            <span style={{textTransform:"uppercase", fontSize:15, textShadow:"1px 1px 0 #000",fontFamily:"arial black"}}>
                            Numbers of jobs booked today
                            </span>
                        </div>
                    </div>
                </div> */}
                            
                <div className="col-md-3 m-2 text-white" style={{borderRadius:20,background:"#730360", overflow:"hidden", boxShadow:"0 4px 4px 0 #000"}}>
                    <div className="row align-items-center">
                        <div className="col-md-12 d-flex align-items-center justify-content-center" style={{minHeight:200}}>
                            <div className="row">
                                <div className="col-md-5 text-center">
                                    <h2>
                                        {unrepliedMessages}
                                    </h2>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12 bg-info text-center p-2 text-white" style={{minHeight:50}}>
                            <span style={{textTransform:"uppercase", fontSize:15, textShadow:"1px 1px 0 #000",fontFamily:"arial black"}}>
                            Number Unreplied Messages 
                            </span>
                        </div>
                    </div>
                </div>

                <div className="col-md-3 m-2 text-dark" style={{borderRadius:20,background:"#00fbff", overflow:"hidden", boxShadow:"0 4px 4px 0 #000"}}>
                    <div className="row align-items-center">
                        <div className="col-md-12 d-flex align-items-center justify-content-center" style={{minHeight:200}}>
                            <div className="row">
                                <div className="col-md-5 text-center">
                                    <h2>
                                        {registeredUsers}
                                    </h2>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12 bg-info text-center p-2 text-white" style={{minHeight:50}}>
                            <span style={{textTransform:"uppercase", fontSize:15, textShadow:"1px 1px 0 #000",fontFamily:"arial black"}}>
                            Number Registered customers 
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Panel
