import React,{useEffect, useState, useContext} from 'react'
import {AuthAlertContext} from '../../../AuthAlert/AuthAlertContext'

const ModifySize = () => {
    const {setAlert} = useContext(AuthAlertContext)
    const [sizes, setSizes] = useState([])
    const [sizeid, setSizeid] = useState(null)
    const [modal, setModal] = useState('none')
    // const [sizesapi,setSizesapi] = useState(null)
    // const [status,setStatus] = useState(null)
    // const [modstatus,setModstatus] = useState(null)
    const [price,setPrice] = useState(0)
    const [extra,setExtra] = useState(0)
    const [discount,setDiscount] = useState(0)
    const [modifingid, setModifingid] = useState('none')   
    const [sizeType, setSizeType] = useState('ktl-photobook-sizes')

        useEffect(() => {

            getSizes()
        }, [sizeType])

        const getSizes = ()=>{
            let xhr  = new XMLHttpRequest()
            xhr.open('GET', `/admin/${sizeType}`, true)
            xhr.onload = ()=>{
                if (xhr.readyState === 4 && xhr.status ===200) {
                    if (JSON.parse(xhr.responseText)) {                       
                        if (JSON.parse(xhr.responseText).response) {   
                            if (JSON.parse(xhr.responseText).response.state) {
                                let size = JSON.parse(xhr.responseText).response.result
                                setSizes(size)
                                console.log(JSON.parse(xhr.responseText).response.result, 'sizes here ')
                            }
                        }
                    }
                }
            }
            xhr.setRequestHeader('x-auth-token', localStorage.adminToken);
            xhr.send();
        }

        const updateSize = () => {
            if (sizeid) {
                if (sizeType) {
                    if (price) {
                            let dataform = new FormData()
                            dataform.append('price', price)
                            dataform.append('extra', extra)
                            dataform.append('discount', discount)
                            dataform.append('id', sizeid)
                            dataform.append('type', sizeType)
                            let sxhr = new XMLHttpRequest();
                            sxhr.open('POST', '/admin/modify-size', true)
                            sxhr.onload = ()=>{
                                if (sxhr.status == 200 && sxhr.readyState==4) {
                                    if (sxhr.responseText) {
                                        if (JSON.parse(sxhr.responseText)) {
                                            if (JSON.parse(sxhr.responseText).response) {
                                                if (JSON.parse(sxhr.responseText).response.state) {
                                                    console.log(sxhr.responseText)
                                                    setModal("none")
                                                    setAlert(JSON.parse(sxhr.responseText).response.message,'success')
                                                    getSizes()
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                            sxhr.setRequestHeader('x-auth-token', localStorage.adminToken);
                            sxhr.send(dataform)
                    }
                }
            }
        }

        const deletesize = ()=>{
            if (sizeid) {
                let dataform = new FormData();
                dataform.append('id', sizeid)
                dataform.append('type', sizeType)
                let delxhr = new XMLHttpRequest();
                delxhr.open('POST', '/admin/delete-size', true)
                delxhr.onload = ()=>{
                    if (delxhr.readyState ==4 && delxhr.status) {
                        if (delxhr.responseText) {
                            if (JSON.parse(delxhr.responseText)) {
                                if (JSON.parse(delxhr.responseText).response) {
                                    if (JSON.parse(delxhr.responseText).response.state) {
                                        setModal("none")
                                        setAlert(JSON.parse(delxhr.responseText).response.message,'danger')
                                        getSizes()
                                    }
                                }
                            }   
                        }
                    }
                }
                delxhr.setRequestHeader('x-auth-token', localStorage.adminToken);
                delxhr.send(dataform);
            }
    
        }

        return (
            <div className="col-md-12 p-4 bg-light" style={{overflowX:'auto'}}>
                {/* modal start  */}

                <div className="" style={{display:`${modal}`,overflowY:'auto', overflowX:"hidden",justifyContent:"center", alignItems:"center", flex:1, position:"fixed",bottom:0, top:0,left:0,right:0, width:"100%", height:"100%", background:"linear-gradient(90deg, rgba(207, 207, 207,0.8), rgba(207, 207, 207,0.8))", zIndex:6}}>
                    <div className="" style={{display:"flex"}}>
                    <div className="col-md-12 " style={{}}>
                        <div className="row justify-content-center align-items-center" style={{background:"#f3f3f3", overflow:"hidden", borderRadius:12, padding:20}}>
                            <div className="row justify-content-center">
                            <div className="mb-3 p-3 text-center text-danger" style={{borderRadius:50, position:"fixed", top:10, right:10, zIndex:5}} 
                                onClick={()=>{
                                    setModal('none')
                                    console.log('you just clicked me ')
                                }}> 
                            <span className={'fa fa-times'} style={{fontSize:50}}>
                                    </span>
                            </div>
                            <div className="col-md-2" style={{minHeight:300, minWidth:500}}>

                                <div className="row">
                                    <div className="col-md-12 bg-light p-4" style={{borderRadius:5}}>
                                    <div className="form-group mb-5">
                                        <span className="btn btn-danger form-control" 
                                            onClick={()=>deletesize(modifingid)}
                                        >
                                                Delete size with id {sizeid}
                                            </span>
                                        </div>
                                        <div className="form-group mb-3">
                                            <label htmlFor="ktl_size_price">Price</label>
                                            <input type="number" className="form-control" id="ktl_size_price" value={price} onChange={(e)=>{
                                                setPrice(e.target.value);
                                            }}/>
                                        </div>
                                        <div className="form-group mb-3">
                                            <label htmlFor="ktl_size_extra">Extras price</label>
                                            <input type="number" className="form-control" id="ktl_size_extra" value={extra} onChange={(e)=>{
                                                setExtra(e.target.value);
                                            }}/>
                                        </div>

                                        <div className="form-group mb-3">
                                            <label htmlFor="ktl_size_discount">Discount</label>
                                            <input type="number" className="form-control" id="ktl_size_discount" value={discount} onChange={(e)=>{
                                                setDiscount(e.target.value);
                                            }}/>
                                        </div>

                                        <div className="form-group mb-3"
                                            onClick={()=>updateSize()}
                                        >
                                        <span className="btn btn-success form-control"
                                        >
                                            Save changes to size with id {sizeid}
                                            </span>
                                        </div>
                                    </div>
                                </div>
                                </div>
                        </div>
                    </div>
                    </div>
                    </div>
                </div>

                {/* modal ends  */}
                <div className="row">
                    <div className="col-md-12">
                    <div className="form-group row mb-4">
                    <select className="custom-select my-1 mr-sm-2" id="inlineFormCustomSelectPref" onChange={(e)=>{
                                setSizeType(e.target.value)
                    }} required>
                        <option value="ktl-photobook-sizes">Photobook</option>
                        <option value="ktl-enlargement-sizes">Enlargement</option>
                        <option value="ktl-potrait-sizes">Potrait</option>
                    </select>
                    </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12">
                    <table className="table table-striped">
                <thead className="thead">
                    <tr>
                        <th scope="col"> ID</th>
                        <th scope="col">Size</th>
                        <th scope="col">Type</th>
                        <th scope="col">Price</th>
                        <th scope="col">Extras </th>
                        <th scope="col">%off</th>
                        <th scope="col">Action</th>
                    </tr>
                </thead>
                <tbody>
                {sizes && sizes.map(size=>(

                    <tr>
                        <th scope="row">{size.id}</th>
                        <td>{size.size}</td>
                        <td>{size.type}</td>
                        <td>{size.price}</td>
                        <td>{size.extra_price}</td>
                        <td>{size.discount_price}</td>
                        <td>
                            <span className="text-info"
                             onClick={()=>{
                                 setModal("flex");
                                 setSizeid(size.id);
                                 setPrice(size.price);
                                 setDiscount(size.discount_price);
                                 setExtra(size.extra_price)
                            }} style={{cursor:"pointer", padding:10, borderRadius:10, background:'#fff'}}>Modify</span>
                        </td>
                    </tr>
                ))}
                    </tbody>
                </table>
                    </div>
                </div>
            </div>
        )
}

export default ModifySize
