import React,{useState, useEffect, useContext} from 'react'
import {AuthAlertContext} from '../../../AuthAlert/AuthAlertContext'


export const AddSize = () => {
    const {setAlert} = useContext(AuthAlertContext)
    const [width, setWidth] = useState(null)
    const [height, setHeight] = useState(null)
    const [price, setPrice] = useState(null)
    const [discount, setDiscount] = useState(0)
    const [type, setType] = useState(null)
    const [extra, setExtra] = useState(null)
    const [jobType, setJobType] = useState(null)
    

    const addSize = ()=>{
        if (width) {
            if (height) {
                if (price) {
                    if (jobType) {
                        let dataForm = new FormData()
                        dataForm.append('size', `${width} by ${height}`)
                        dataForm.append('price', price)
                        dataForm.append('discount', discount)
                        dataForm.append('type', type)
                        dataForm.append('jobType', jobType)
                        dataForm.append('extra', extra)
                        let sizexhr = new XMLHttpRequest();
                        sizexhr.open('POST', '/admin/add-size', true)
                        sizexhr.onload = ()=>{
                            if (sizexhr.readyState ==4 && sizexhr.status ==200) {
                                console.log(JSON.parse(sizexhr.responseText));
                                    if (sizexhr.responseText) {
                                        if (JSON.parse(sizexhr.responseText)) {
                                            if (JSON.parse(sizexhr.responseText).response) {
                                                if (JSON.parse(sizexhr.responseText).response.state) {
                                                    setDiscount(0)
                                                    setHeight(null)
                                                    setPrice(null)
                                                    setType(null)
                                                    setWidth(null)
                                                    setAlert(JSON.parse(sizexhr.responseText).response.message, 'success')
                                                    let formsinput = document.querySelectorAll('input')
                                                    formsinput.forEach((input)=>{
                                                        input.value = null
                                                    })
                                                    console.log(sizexhr.responseText);
                                                }
                                            }
                                        }
                                    }
                            }
                        }
                        sizexhr.setRequestHeader('x-auth-token', localStorage.adminToken)
                        sizexhr.send(dataForm)
                    }
                }
            }
        }
    }

    return (
        <div className="col-md-12">
            <div className="row justify-content-center">
               <div className="col-md-8" style={{background:"#fefefe", padding:30, borderRight:"20px solid #eee",  borderLeft:"20px solid #eee"}}>
                   <h2 className="p-3">Add new canvas size </h2>
               <form>
                    <div className="form-group row">
                        <label htmlFor="colFormLabel">Canvas height</label>
                            <div className="col-12">
                            <input type="number" className="form-control"  placeholder="enter height" onChange={(e)=>{
                                setHeight(e.target.value)
                            }} required/>
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="colFormLabel">Canvas width (required)</label>
                            <div className="col-12">
                            <input type="number" className="form-control"  placeholder="enter width" onChange={(e)=>{
                                setWidth(e.target.value)
                            }} required/>
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="colFormLabel">Price (required)</label>
                            <div className="col-12">
                            <input type="number" className="form-control"  placeholder="enter price" onChange={(e)=>{
                                setPrice(e.target.value)
                            }} required/>
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="colFormLabel">Discount (optional)</label>
                            <div className="col-12">
                            <input type="number" className="form-control"  placeholder="enter discount" onChange={(e)=>{
                                setDiscount(e.target.value)
                            }} />
                        </div>
                    </div>
                    <div className="form-group row">
                    <label className="my-1 mr-2">Job type</label>
                    <select className="custom-select my-1 mr-sm-2" id="inlineFormCustomSelectPref" onChange={(e)=>{
                                setJobType(e.target.value)
                    }} required>
                        <option selected>Choose...</option>
                        <option value="photobook">Photobook</option>
                        <option value="enlargement">Enlargement</option>
                        <option value="potrait">Potrait</option>
                    </select>
                    </div>
                    {jobType == 'photobook' && <div className="form-group row">
                    <label className="my-1 mr-2">Photobook Type</label>
                    <select className="custom-select my-1 mr-sm-2" id="inlineFormCustomSelectPref" onChange={(e)=>{
                                setType(e.target.value)
                    }} required>
                        <option selected>Choose...</option>
                        <option value="synthetic">Synthetic</option>
                        <option value="layflat">Layflat</option>
                    </select>
                    </div>
                    }
                    {jobType == 'photobook' || jobType == 'enlargement' ? (<div className="form-group row">
                        <label htmlFor="colFormLabel">Extras price</label>
                            <div className="col-12">
                            <input type="number" className="form-control"  placeholder="enter price for packaging" onChange={(e)=>{
                                setExtra(e.target.value)
                            }} required/>
                        </div>
                    </div>): null
                    }
                    <div className="form-group row">
                            <div className="col-12">
                            <input type="button" Value="Add Size " className="form-control btn btn-info"  placeholder="col-form-label" onClick={()=>addSize()} />
                        </div>
                    </div>
                </form>
               </div>
            </div>
        </div>
    )
}

export default AddSize
