import React, {useContext, useEffect, useState} from 'react'
import Pagination from '../../../Dashboard/Users/Bookjob/Pagination'

export const Users = () => {
    const [users, setUsers] = useState([])
    const [currentPage, setCurrentPage] = useState(1)
    const [postPerPage, setPostPerPage] = useState(10)

    useEffect(() => {
        fetchUsers()
        console.log(currentPost,' this is the current post ')
    }, [])

    const indexOfLastPost = currentPage * postPerPage;
    const indexOfFirstPost = (indexOfLastPost - postPerPage);
    const currentPost = users.slice(indexOfFirstPost, indexOfLastPost);

    const setPage = (number)=>{
        setCurrentPage(number)
    }
    

    const fetchUsers=()=>{
        let xhr = new XMLHttpRequest();
        xhr.open('GET', '/admin/ktl-users', true)
        xhr.onload = ()=>{
            if (xhr.readyState == 4 && xhr.status == 200) {
                if (xhr.responseText) {
                    if (JSON.parse(xhr.responseText)) {
                        if (JSON.parse(xhr.responseText).response) {
                            if (JSON.parse(xhr.responseText).response.state) {
                                setUsers(JSON.parse(xhr.responseText).response.result)
                            }
                        }
                    }   
                }
            }
        }
        xhr.setRequestHeader('x-auth-token', localStorage.adminToken)
        xhr.send();
    }

    return (
        <div className="col-md-12 p-4 bg-light" style={{overflowX:'auto'}}>

<           section className="content">
            <div className="card" style={{overflowX:'hidden'}}>
                <div className="card-header">
                <h3 className="card-title">KingsTyme Users  </h3>
                </div>
                <div className="card-body p-0" style={{display: 'block'}} style={{overflowX:'scroll'}}>
                <table className="table table-striped projects" >
                    <thead>
                        <tr>
                            <th scope="col">User ID</th>
                            <th scope="col">Username</th>
                            <th scope="col">Phone</th>
                            <th scope="col-2">Address</th>
                            <th scope="col-2">Email</th>
                        </tr>
                    </thead>
                    <tbody>

                    {currentPost.map((user, key)=>(
                        <tr key={key} >
                            <td>{user.user_id}</td>
                            <td>{user.user_name}</td>
                            <td>{user.user_phone}</td>
                            <td>{user.user_address}</td>
                            <td>{user.user_email}</td>
                        </tr>
                        ))}
                    </tbody>
                </table>
                </div>
                <Pagination postPerPage={postPerPage} setPage={setPage} totalPost={users.length} />
            </div>
            </section>
        </div>
    )
}

export default Users